<template>
  <div class="table-tree-container">
    <el-container>

      <!--    -->
      <el-header>
        <Header ref="header"
                :handleType="handleType"
                :level="level"
                :id="id"
                :id-factory="idFactory"
                :tree-data="treeData"
                :factory-data="factoryData"
                :pass-list="passList"
                :plugin-list="pluginList"
                :pass-type-list="passTypeList"
                :sata-list="sataList"
                :baud-list="baudList"
                :data-list="dataList"
                :check-list="checkList"
                :equipment-list="equipmentList"
                @item-add="itemAdd"
                @items-copy="itemsCopy"
                @item-delete="itemHandle('del')"
                @item-submit="itemSubmit"
                @factory-select="factorySelect"></Header>
      </el-header>

      <!--    -->
      <el-container>

        <!--  -->
        <el-aside class="left-panel"
                  :style="{height:contentHeight}">
          <div class="left-panel-title"></div>
          <left-tree v-if="treeData.length!==0"
                     class="left-panel-tree"
                     :data="treeData"
                     :id="id"
                     @item-click="itemClick"
                     @item-handle="itemHandle">
          </left-tree>
          <div v-else
               class="left-panel-tree"></div>
        </el-aside>

        <!--  -->
        <el-container class="right-panel is-vertical">
          <Group v-if="level===1"
                 ref="group"
                 :service-id="serviceId"
                 :factory-data="factoryData"
                 :service-data="serviceData"
                 :pass-list="passList"
                 :equipment-list="equipmentList"
                 :pass-type-list="passTypeList"
                 :sata-list="sataList"
                 :baud-list="baudList"
                 :data-list="dataList"
                 :check-list="checkList"
                 :stop-list="stopList"></Group>
          <Pass v-if="level===2"
                ref="pass"
                :id="id"
                :tree-data="treeData"
                :pass-list="passList"
                :equipment-list="equipmentList"
                :plugin-list="pluginList"
                :pass-type-list="passTypeList"
                :sata-list="sataList"
                :baud-list="baudList"
                :data-list="dataList"
                :check-list="checkList"
                :stop-list="stopList"></Pass>
          <Equipment v-if="level===3"
                     ref="equipment"
                     :id="id"
                     :tree-data="treeData"
                     :equipment-list="equipmentList"></Equipment>
        </el-container>

      </el-container>

    </el-container>
  </div>
</template>

<script>
import Header from "@/components/Header"; // 
import LeftTree from "@/components/Tree"; // 
import Group from "@/components/content/Group"; //  - 
import Pass from "@/components/content/Pass"; //  - 
import Equipment from "@/components/content/Equipment"; //  - 
/* mockData */
import { factoryData } from "@/mock/tree.js"; // mockData - 
import { pluginList } from "@/mock/plugin.js"; // mockData - 
import {
  passList, // mockData - 
  equipmentList // mockData - 
} from "@/mock/content.js";

export default {
  components: { Header, LeftTree, Group, Pass, Equipment },
  data () {
    return {
      /* data */
      treeData: [], // 
      factoryData: [], // 
      passList: [], // 
      pluginList: [], // 
      equipmentList: [], // 
      serviceData: {}, // 
      serviceId: "", // id
      passTypeList: ["", "TCP", "TCP", "UDP", ""], // select - 
      sataList: ["COM01", "COM02", "COM03"], // 
      baudList: ["1200", "2400", "4800", "9600"], // 
      dataList: ["4", "5", "6", "7", "8"], // 
      checkList: ["", "", "", "MARK", "SPACE"], // 
      stopList: ["1", "1.5", "2"], // 
      /* tree */
      level: 1, //  - 
      id: null, // id - 
      handleType: "", // 
      idFactory: "factory-1", // id - 
      /*  */
      contentHeight: "0px" // 
    };
  },
  created () {
    const screenHeight = document.documentElement.clientHeight;
    // console.log(screenHeight);
    this.contentHeight = (screenHeight - 8 * 2 - 74) + "px";
    this.getTreeData();
  },
  methods: {
    // 
    getTreeData () {
      this.factoryData = factoryData;
      this.factoryData[0].children.forEach(group => {
        group.children.forEach(factory => {
          if (factory.selected) {
            this.serviceData = factory;
            this.serviceId = this.serviceData.id;
            this.treeData = factory.treeData;
            this.id = this.treeData[0].id;
          }
        });
      });
      this.pluginList = pluginList;
      this.passListAll = passList;
      this.equipmentListAll = equipmentList;
      this.refreshData();
    },
    // 
    refreshData () {
      this.passList = this.passListAll.filter(pass => pass.idFactory === this.idFactory);
      this.equipmentList = this.equipmentListAll.filter(equipment => equipment.idFactory === this.idFactory);
      // console.log(this.passList);
      // console.log(this.equipmentList);
    },
    // 
    itemClick (param) {
      // console.log(param);
      const { level, id } = param;
      this.level = level;
      this.id = id;
    },
    //  - 
    itemHandle (type) {
      this.handleType = type;
      if (this.handleType === "up" || this.handleType === "down") { //  / 
        this.treeData.forEach((service, i) => {
          if (this.handleType === "up") { //  -> service.children
            service.children.forEach((pass, _i) => {
              if (this.level === 2 && pass.id === this.id) { // 
                if (!service.children[_i - 1]) {
                  this.moveEnd("");
                  return false;
                }
                this.toggleExchange(service.children, _i, "up");
                pass.selected = false; // 
                this.refreshSelect(); //  ""
              }
              if (this.level === 3) { //  -> pass.children
                pass.children.forEach((equipment, __i) => {
                  if (equipment.id === this.id) {
                    if (!pass.children[__i - 1]) {
                      this.moveEnd("");
                      return false;
                    }
                    this.toggleExchange(pass.children, __i, "up");
                    equipment.selected = false; // 
                    this.refreshSelect(); //  ""
                  }
                });
              }
            });
          }
          if (this.handleType === "down") { //  -> service.children
            for (let _i = service.children.length - 1; _i >= 0; _i--) {
              if (this.level === 2 && service.children[_i].id === this.id) { // 
                if (!service.children[_i + 1]) {
                  this.moveEnd("");
                  return false;
                }
                this.toggleExchange(service.children, _i, "down");
                service.children[_i + 1].selected = false; // 
                this.refreshSelect(); //  ""
              }
              if (this.level === 3) { //  -> service.children[_i].children
                for (let __i = service.children[_i].children.length - 1; __i >= 0; __i--) {
                  if (service.children[_i].children[__i].id === this.id) {
                    if (!service.children[_i].children[__i + 1]) {
                      this.moveEnd("");
                      return false;
                    }
                    this.toggleExchange(service.children[_i].children, __i, "down");
                    service.children[_i].children[__i + 1].selected = false; // 
                    this.refreshSelect(); //  ""
                  }
                }
              }
            }
          }
        });
      }
      if (this.handleType === "del") { // 
        this.$confirm(`${this.level === 2 ? "" : ""}, ?`, "", {
          type: "warning"
        }).then(() => {
          this.treeData.forEach(service => {
            service.children.forEach((pass, i) => {
              /*  */
              if (this.level === 2 && this.id === pass.id) {
                pass.children.forEach((equipment, _i) => { //  - 
                  this.equipmentListAll.forEach((_equipment, __i) => {
                    equipment.id === _equipment.id && this.equipmentListAll.splice(__i, 1);
                  });
                });
                service.children.splice(i, 1); // 
                this.passListAll.forEach((_pass, _i) => { // 
                  _pass.id === pass.id && this.passListAll.splice(_i, 1);
                });
                this.refreshData(); // 
              }
              /*  */
              this.level === 3 && pass.children.forEach((equipment, _i) => {
                if (this.id === equipment.id) {
                  pass.children.splice(_i, 1); // 
                  this.equipmentListAll.forEach((_equipment, __i) => { // 
                    _equipment.id === equipment.id && this.equipmentListAll.splice(__i, 1);
                  });
                }
              });
            });
          });
          this.refreshData(); // 
          this.refreshSelect(); //  ""
          // console.log(this.treeData);
        }).catch(() => { });
      }
      if (this.handleType === "add") { // 
        this.$refs.header.newBuild();
      }
      if (this.handleType === "copy") { // 
        this.$refs.header.copyItems();
      }
    },
    //  ""
    refreshSelect () {
      this.$nextTick(() => {
        this.level = 1;
        this.id = this.treeData[0].id;
        this.$set(this.treeData[0], "selected", true);
      });
    },
    // /message
    moveEnd (msg) {
      this.$message({
        message: msg,
        type: "error",
        duration: 1000
      });
    },
    // /arrindex(type:up)/(type:down)
    toggleExchange (arr, index, type) {
      const toExchange = type === "up" ? arr[index - 1] : arr[index + 1]; // copy
      this.$set(
        arr,
        type === "up" ? index - 1 : index + 1,
        arr[index]
      ); // copy
      this.$set(arr, index, toExchange); // copy
    },
    // 
    itemAdd (formData) {
      // console.log(formData);
      this.treeData.forEach(service => {
        /*  */
        if (this.level === 1 && service.id === this.id) {
          formData.id = this.id + Math.random().toString(36).substr(-10); // id
          formData.idFactory = this.idFactory; // id
          const obj = {
            text: `${formData.passName}[${formData.passDescribe}]`,
            icon: "fa fa-laptop",
            id: formData.id,
            level: 2,
            children: [],
            selected: false,
            opened: true // openedtrue
          };
          service.children.push(obj); // 
          this.passListAll.push(formData); // 
          this.refreshData(); // 
        }
        /*  */
        this.level === 2 && service.children.forEach(pass => {
          if (pass.id === this.id) {
            formData.id = Math.random().toString(36).substr(-10); // id
            formData.idFactory = this.idFactory; // id
            const obj = {
              text: `${formData.equipmentName}[${formData.equipmentDescribe}]`,
              icon: "fa fa-edit",
              id: formData.id,
              level: 3,
              selected: false
            };
            pass.children.push(obj); // 
            this.equipmentListAll.push(formData); // 
            this.refreshData(); // 
          }
        });
      });
      // console.log(this.treeData);
      this.$refs.group && this.$nextTick(() => {
        this.$refs.group.getServiceData(); // table
      });
    },
    // 
    itemsCopy (multipleSelection) {
      // console.log(multipleSelection);
      this.treeData.forEach(service => {
        /*  */
        if (this.level === 1 && service.id === this.id) {
          multipleSelection.forEach(selection => {
            const selectionCopy = JSON.parse(JSON.stringify(selection)); // 
            selectionCopy.id = this.id + Math.random().toString(36).substr(-10); // id
            const obj = {
              text: `${selectionCopy.passName}[${selectionCopy.passDescribe}]`,
              icon: "fa fa-laptop",
              id: selectionCopy.id,
              level: 2,
              children: [],
              selected: false,
              opened: true // openedtrue
            };
            service.children.push(obj); // 
            this.passListAll.push(selectionCopy); // 
          });
          this.refreshData(); // 
        }
        /*  */
        this.level === 2 && service.children.forEach(pass => {
          if (pass.id === this.id) {
            multipleSelection.forEach(selection => {
              const selectionCopy = JSON.parse(JSON.stringify(selection)); // 
              selectionCopy.id = this.id + Math.random().toString(36).substr(-10); // id
              const obj = {
                text: `${selectionCopy.equipmentName}[${selectionCopy.equipmentDescribe}]`,
                icon: "fa fa-edit",
                id: selectionCopy.id,
                level: 3,
                selected: false
              };
              pass.children.push(obj); // 
              this.equipmentListAll.push(selectionCopy); // 
            });
            this.refreshData(); // 
          }
        });
      });
      // console.log(this.treeData);
    },
    //  - 
    factorySelect (param) {
      // console.log(param);
      const { level, id } = param;
      if (level === 3) {
        this.serviceData = param;
        this.serviceId = this.serviceData.id;
        this.idFactory = id;
        this.treeData = param.treeData;
        if (this.treeData.length !== 0) { // 
          this.treeData.forEach(group => { // 
            group.selected = false;
            group.children.forEach(pass => {
              pass.selected = false;
              pass.children.forEach(equipment => {
                equipment.selected = false;
              });
            });
          });
          this.refreshSelect(); //  ""
        }
        this.refreshData();
      }
    },
    // 
    itemSubmit (level) {
      level === 2 && this.$refs.pass.passSubmit();
      level === 3 && this.$refs.equipment.equipmentSubmit();
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
@import '~@/styles/smart-plan.scss';
</style>



