<template>
  <el-dialog class="tag-select-dialog"
             v-if="dialogVisible"
             title=""
             :visible.sync="dialogVisible">

    <el-container>

      <!--  -->
      <el-aside class="left-panel">
        <left-tree class="left-panel-tree"
                   :data="tagTreeData"
                   :contextmenu="false"
                   :collapse="false"
                   @item-click="itemClick">
        </left-tree>
      </el-aside>

      <!--  -->
      <el-container class="right-panel is-vertical">
        <el-form label-position="left">

          <el-form-item label-width="55px"
                        label="">
            <el-input v-model="formData.IOTag"
                      disabled></el-input>
          </el-form-item>
          <el-form-item label-width="55px"
                        label="">
            {{tagDescribe}}
          </el-form-item>

          <el-table ref="tagListTable"
                    :data="tableData"
                    height="350"
                    tooltip-effect="dark"
                    highlight-current-row
                    @current-change="tagClick">
            <el-table-column type="index"
                             label=""
                             width="40">
            </el-table-column>
            <el-table-column prop="name"
                             label=""
                             width="110">
            </el-table-column>
            <el-table-column prop="describe"
                             label=""
                             width="140">
            </el-table-column>
            <el-table-column prop="dataType"
                             label=""
                             width="70">
            </el-table-column>
            <el-table-column prop="direction"
                             label=""
                             width="60">
            </el-table-column>
            <el-table-column prop="source"
                             label=""
                             width="70">
            </el-table-column>
          </el-table>

        </el-form>
      </el-container>

    </el-container>

    <div slot="footer"
         class="dialog-footer">
      <el-button @click="dialogVisible = false;
                         formData.IOTag = IOTagOrg;
                         formData.IOTagParentId = IOTagParentIdOrg;
                         formData.IOTagSelectId = IOTagSelectIdOrg"> </el-button>
      <el-button type="primary"
                 @click="dialogVisible = false"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import LeftTree from "@/components/Tree"; // 

export default {
  components: { LeftTree },
  props: {
    // id
    id: {
      type: String
    },
    // 
    formData: {
      type: Object,
      default: () => { }
    },
    //  - 
    treeData: {
      type: Array,
      default: () => []
    },
    // 
    passList: {
      type: Array,
      default: () => []
    },
    // 
    equipmentList: {
      type: Array,
      default: () => []
    },
    // 
    tagDescribe: {
      type: String
    }
  },
  data () {
    return {
      dialogVisible: false, // 
      tableData: [] // 
    };
  },
  methods: {
    // 
    tagSelect () {
      this.dialogVisible = true;
      // console.log(this.formData);
      this.getTagTreeData();
      /*  */
      this.IOTagOrg = JSON.parse(JSON.stringify(this.formData.IOTag));
      this.IOTagParentIdOrg =
        this.formData.IOTagParentId ? JSON.parse(JSON.stringify(this.formData.IOTagParentId)) : "";
      this.IOTagSelectIdOrg =
        this.formData.IOTagSelectId ? JSON.parse(JSON.stringify(this.formData.IOTagSelectId)) : "";
    },
    // 
    getTagTreeData () {
      this.tagTreeData = JSON.parse(JSON.stringify(this.treeData));
      /* group */
      this.tagTreeData.forEach(group => {
        if (group.id === "1") {
          group.text = "IO";
          group.tagSelectList = [
            {
              name: "_kernal_version",
              describe: "",
              dataType: "",
              direction: "",
              source: "IO",
              parentId: group.id,
              id: "0"
            },
            {
              name: "_cpu",
              describe: "CPU",
              dataType: "",
              direction: "",
              source: "IO",
              parentId: group.id,
              id: "1"
            },
            {
              name: "_mem_total",
              describe: "(k)",
              dataType: "",
              direction: "",
              source: "IO",
              parentId: group.id,
              id: "2"
            }
          ];
        } else if (group.id === "2") {
          group.text = "";
          group.children = [];
        };
        /* pass */
        group.children.forEach(pass => {
          this.passList.forEach(_pass => {
            if (pass.id === _pass.id) {
              pass.tagSelectList = [
                {
                  name: "_send_byte",
                  describe: "(BYTE)",
                  dataType: "",
                  direction: "",
                  source: "IO",
                  passName: _pass.passName,
                  passDescribe: _pass.passDescribe,
                  parentId: pass.id,
                  id: "0"
                },
                {
                  name: "_rev_byte",
                  describe: "(BYTE)",
                  dataType: "",
                  direction: "",
                  source: "IO",
                  passName: _pass.passName,
                  passDescribe: _pass.passDescribe,
                  parentId: pass.id,
                  id: "1"
                },
                {
                  name: "_io_status",
                  describe: "",
                  dataType: "",
                  direction: "",
                  source: "IO",
                  passName: _pass.passName,
                  passDescribe: _pass.passDescribe,
                  parentId: pass.id,
                  id: "2"
                }
              ];
              /* equipment */
              pass.children.forEach(equpiment => {
                this.equipmentList.forEach(_equipment => {
                  if (equpiment.id === _equipment.id) {
                    equpiment.tagSelectList = [
                      {
                        name: "_send_package",
                        describe: "",
                        dataType: "",
                        direction: "",
                        source: "IO",
                        passName: _pass.passName,
                        passDescribe: _pass.passDescribe,
                        equpimentName: _equipment.equipmentName,
                        equipmentDescribe: _equipment.equipmentDescribe,
                        parentId: equpiment.id,
                        id: "0"
                      },
                      {
                        name: "_rev_package",
                        describe: "",
                        dataType: "",
                        direction: "",
                        source: "IO",
                        passName: _pass.passName,
                        passDescribe: _pass.passDescribe,
                        equpimentName: _equipment.equipmentName,
                        equipmentDescribe: _equipment.equipmentDescribe,
                        parentId: equpiment.id,
                        id: "1"
                      },
                      {
                        name: "_success_rate",
                        describe: "",
                        dataType: "",
                        direction: "",
                        source: "IO",
                        passName: _pass.passName,
                        passDescribe: _pass.passDescribe,
                        equpimentName: _equipment.equipmentName,
                        equipmentDescribe: _equipment.equipmentDescribe,
                        parentId: equpiment.id,
                        id: "2"
                      },
                      {
                        name: "_io_status",
                        describe: "",
                        dataType: "",
                        direction: "",
                        source: "IO",
                        passName: _pass.passName,
                        passDescribe: _pass.passDescribe,
                        equpimentName: _equipment.equipmentName,
                        equipmentDescribe: _equipment.equipmentDescribe,
                        parentId: equpiment.id,
                        id: "3"
                      }
                    ];
                    _equipment.dataTags.forEach(tag => {
                      equpiment.tagSelectList.push({
                        name: tag.name,
                        describe: tag.describe,
                        dataType: tag.dataType,
                        direction: tag.direction,
                        source: "IO",
                        passName: _pass.passName,
                        passDescribe: _pass.passDescribe,
                        equpimentName: _equipment.equipmentName,
                        equipmentDescribe: _equipment.equipmentDescribe,
                        parentId: equpiment.id,
                        id: tag.id
                      });
                    });
                  }
                });
              });
            }
          });
        });
      });
      // console.log(this.tagTreeData);
    },
    // 
    itemClick (param) {
      this.tableData = param.tagSelectList;
    },
    // 
    tagClick (val) {
      this.$emit("tag-click", val);
    },
    //  - dialog1. 2. 3.
    visibleSelect (item) {
      item.selected = false; // 
      if (item.id === this.formData.IOTagParentId) { // id
        item.selected = true; // 
        this.tableData = item.tagSelectList; // table
        this.$nextTick(() => { // table
          this.tableData.forEach((row, i) => {
            row.id === this.formData.IOTagSelectId && this.$refs.tagListTable.setCurrentRow(this.tableData[i]);
          });
        });
      }
    }
  },
  watch: {
    dialogVisible (val) {
      // console.log(this.formData);
      val === true && this.tagTreeData.forEach(group => {
        this.visibleSelect(group);
        group.children.forEach(pass => {
          this.visibleSelect(pass);
          pass.children.forEach(equipment => {
            this.visibleSelect(equipment);
          });
        });
      });
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
</style>



