/**
 * ['','',''...]3
 * ["DM35S-3/28","DM35S-6/14","DM35R-3/28","DM35R-6/33"] => ["DM35","S","3/28"]
 * @param {Array} array 
 */
export const listToCascader = array => {
  let cascaderValue = [];
  let casList1 = [];
  let casList2 = [];

  //  key-value 
  array.forEach(string => {
    // casList14 +  -> 
    casList1.push(string.substring(0, 4));
    casList1 = Array.from(new Set(casList1));

    // casList2 "-"  +  -> 
    casList2.push(string.split("-")[0]);
    casList2 = Array.from(new Set(casList2));

    // data
  });

  // 
  casList1.forEach(string1 => {
    const casList2Value = [];
    casList2.forEach(string2 => {
      const casList3Value = [];
      array.forEach(string3 => {
        string3.indexOf(string2) > -1 &&
          casList3Value.push({
            value: string3,
            label: string3
          });
      });
      string2.indexOf(string1) > -1 &&
        casList2Value.push({
          value: string2,
          label: string2,
          children: casList3Value
        });
    });
    cascaderValue.push({
      value: string1,
      label: string1,
      children: casList2Value
    });
  });

  return cascaderValue;
};

/**
 * iview(2)
 *  =>
 * @param {Array} array 
 */
export const computedMenuData = array => {
  let treeData = [];

  // 
  array.forEach(menu => {
    menu.parenetId === "root" &&
      treeData.push({
        id: menu.id,
        name: menu.name,
        title: menu.title,
        url: menu.url,
        path: menu.path,
        sort: menu.sort,
        parenetId: menu.parenetId,
        parenetPath: menu.parenetPath,
        showLevel: menu.showLevel,
        isOutSide: menu.isOutSide,
        status: menu.status,
        description: menu.description,
        ico: menu.ico,
        children: []
      });
  });

  // 
  const handleRecurrence = recurrenceData => {
    recurrenceData.forEach(data => {
      array.forEach(menu => {
        data.id === menu.parenetId &&
          data.children.push({
            id: menu.id,
            name: menu.name,
            title: menu.title,
            url: menu.url,
            path: menu.path,
            sort: menu.sort,
            parenetId: menu.parenetId,
            parenetPath: menu.parenetPath,
            showLevel: menu.showLevel,
            isOutSide: menu.isOutSide,
            status: menu.status,
            description: menu.description,
            ico: menu.ico,
            children: []
          });
      });
      // console.log(data);
      handleRecurrence(data.children);
    });
  };
  handleRecurrence(treeData);

  // 
  treeData.length > 0 && (treeData[0].expand = true);
  return treeData;
};

/**
 * [{},{},{}...]object key
 * @param {String} key key
 * @param {String} sort /asc/descasc
 */
export const arraySort = (key, sort) => {
  return function(a, b) {
    if (sort === "asc" || sort === undefined || sort === "") {
      // a[key] > b[key]
      if (a[key] > b[key]) return 1;
      else if (a[key] < b[key]) return -1;
      else return 0;
    } else if (sort === "desc") {
      // a[key] < b[key]
      if (a[key] < b[key]) return 1;
      else if (a[key] > b[key]) return -1;
      else return 0;
    }
  };
};

/**
 * [{},{},{}...]keyvaluekeyvalue
 * @param {Array} array 
 * @param {String} queryKey key
 * @param {String} queryValue value
 * @param {String} getKey key
 */
export const getValueByKey = (array, queryKey, queryValue, getKey) => {
  var getValue = "";
  array.forEach(item => {
    item[queryKey].toString() === queryValue && (getValue = item[getKey]);
  });
  return getValue;
};

/**
 * excel
 * labelkey
 * @param {Object} tableHeader 
 */
export const setKeyFromTableHeader = tableHeader => {
  // 1.
  var array = []; // 
  var arrayCopy = []; // copy
  Object.keys(tableHeader).forEach((key, i) => {
    array.push(key);
    arrayCopy.push(key);
  });
  // console.log(array);
  // console.log(arrayCopy);
  // 2.
  var arrayTargetSingle = []; // excellistA1B1...
  var arrayTargetBiliteral = []; // excellistAA1AB1...
  var regex = /^[A-Z]+$/; // 
  for (var i = array.length - 1; i >= 0; i--) {
    var numBiliteral = 0;
    var numSingle = 0;
    for (var _i = arrayCopy.length - 1; _i >= 0; _i--) {
      // excel
      if (
        array[i].slice(0, 2) === arrayCopy[_i].slice(0, 2) &&
        regex.test(array[i][1])
      ) {
        numBiliteral++;
        if (
          numBiliteral > 0 &&
          array[i].slice(0, 1) !== "!" &&
          arrayTargetBiliteral.every(
            item => item.slice(0, 2) !== array[i].slice(0, 2)
          )
        ) {
          arrayTargetBiliteral.push(array[i]);
        }
      }
      // excel
      if (
        array[i].slice(0, 1) === arrayCopy[_i].slice(0, 1) &&
        !regex.test(array[i][1])
      ) {
        numSingle++;
        if (
          numSingle > 0 &&
          array[i].slice(0, 1) !== "!" &&
          arrayTargetSingle.every(
            item => item.slice(0, 1) !== array[i].slice(0, 1)
          )
        ) {
          arrayTargetSingle.push(array[i]);
        }
      }
    }
  }
  // 3.
  arrayTargetBiliteral = [...new Set(arrayTargetBiliteral)].sort();
  arrayTargetSingle = [...new Set(arrayTargetSingle)].sort();
  // console.log(arrayTargetBiliteral);
  // console.log(arrayTargetSingle);
  return arrayTargetSingle.concat(arrayTargetBiliteral);
};

/**
 * [{},{},{}...]key
 * @param {Array} array 
 * @param {String} key key
 * @param {Function} condition 
 */
export const addValueByKey = (array, key, condition = true) => {
  var addValue = 0;
  array.forEach(item => {
    condition && (addValue += Number(item[key]));
  });
  return addValue;
};

