<!--
  https://github.com/zdy1988/vue-jstree/blob/master/README-CN.md
  data  Array 
  size  String  ,  : 'large' or '' or ''small'
  show-checkbox Boolean false 
  allow-transition  Boolean true  
  whole-row Boolean false 
  no-dots Boolean false 
  collapse  Boolean false  opened 
  is-multiple Boolean false 
  allow-batch Boolean false 
  treeName-field-name String  'treeName'     treeName
  value-field-name  String  'value'    value
  children-field-name String  'children'     children
  item-events Object  {}  , 
  async Function   ,  , 'isLeaf: true'  + 
  loading-treeName  String  'Loading'  Loading 
  draggable Boolean false  , ,  'dragDisabled: true'  'dropDisabled: true'
  drag-over-background-color  String  '#C9FDC9' 
  klass String   class
  type-field:    :root/:leaf
  parent-field: 
  isEdit: 
-->
<template>
  <div>

    <!--   -->
    <div v-if="contextmenu"
         @contextmenu.prevent="$refs.ctx.open($event)">
      <v-jstree :data="data"
                :collapse="collapse"
                @item-click="itemClick"
                @item-toggle="itemClick"></v-jstree>
    </div>

    <!--   -->
    <v-jstree v-else
              :data="data"
              :collapse="collapse"
              @item-click="itemClick"></v-jstree>
    <!-- @item-toggle="itemClick" -->

    <!---->
    <context-menu id="ctx"
                  ref="ctx">
      <li class="ctx-header"></li>
      <li v-if="level===1||(level===2&&id&&id.slice(0,1)==='1')"
          class="ctx-item"
          @click="itemHandle('add')">{{level===1?'':""}}</li>
      <li v-if="level!==1"
          class="ctx-item"
          @click="itemHandle('del')">{{level===2?'':""}}</li>
      <li v-if="level===1||(level===2&&id&&id.slice(0,1)==='1')"
          class="ctx-sperator">
        <hr>
      </li>
      <li v-if="level===1||(level===2&&id&&id.slice(0,1)==='1')"
          class="ctx-item"
          @click="itemHandle('copy')">{{level===1?'':""}}</li>
      <li v-if="level!==1&&id&&id.slice(0,1)==='1'"
          class="ctx-sperator">
        <hr>
      </li>
      <li v-if="level!==1&&id&&id.slice(0,1)==='1'"
          class="ctx-item"
          @click="itemHandle('up')">{{level===2?'':""}}</li>
      <li v-if="level!==1&&id&&id.slice(0,1)==='1'"
          class="ctx-item"
          @click="itemHandle('down')">{{level===2?'':""}}</li>
    </context-menu>

  </div>
</template>

<script>
import VJstree from "vue-jstree"; // 
import contextMenu from "vue-context-menu"; // 

export default {
  name: "VueTree",
  components: { VJstree, contextMenu },
  props: {
    // 
    data: {
      type: Array,
      default: () => []
    },
    // id
    id: {
      type: String
    },
    // 
    contextmenu: {
      type: Boolean,
      default: true
    },
    // 
    collapse: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      level: 1, // 
      /* dialog */
      dialogVisible: false, // 
      dialogTitle: "", // 
      isAdd: true //  true of false
    };
  },
  methods: {
    //  - 
    itemClick (node) {
      // console.log(node.model);
      this.level = node.model.level;
      this.$emit("item-click", node.model);
    },
    //  - 
    itemHandle (type) {
      this.$emit("item-handle", type);
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
</style>



