<template>
  <div class="table-component">

    <!-- btns -->
    <el-row style="margin-bottom:20px">
      <el-select v-model="dataTypeSelect"
                 placeholder=""
                 style="width:150px;margin-right:10px"
                 @change="refreshData">
        <el-option v-for="item in dataTypeList"
                   :key="item"
                   :label="item"
                   :value="item">
        </el-option>
      </el-select>
      <el-button type="primary"
                 size="small"
                 plain
                 @click="insertTag"></el-button>
      <el-button type="success"
                 size="small"
                 plain
                 @click="loadTags"></el-button>
      <el-button type="danger"
                 size="small"
                 plain
                 @click="deleteTags"></el-button>
      <el-button type="info"
                 size="small"
                 plain
                 :loading="downloadLoading"
                 @click="downloadTable"></el-button>
      <el-button type="info"
                 size="small"
                 plain
                 @click="uploadTable"></el-button>
      <input ref="excel-upload-input"
             class="excel-upload-input"
             type="file"
             accept=".xlsx, .xls"
             @change="handleClick">
    </el-row>

    <!-- table -->
    <el-table :data="dataTags"
              border
              current-row-key
              empty-text=""
              :max-height="tableMaxHeight"
              @selection-change="handleSelectionChange">

      <el-table-column type="selection"
                       width="30">
      </el-table-column>
      <el-table-column prop="index"
                       label=""
                       width="40">
      </el-table-column>

      <!--  -->
      <el-table-column v-for="(column, index) in dataColumns"
                       :key="index"
                       :prop="column.prop"
                       :label="column.label"
                       :width="column.width?column.width:''"
                       :min-width="column.minWidth?column.minWidth:''">
      </el-table-column>

      <!-- BCD -->
      <el-table-column label="BCD"
                       width="60">
        <template slot-scope="scope">
          {{ scope.row.useBCD?'1':'0' }}
        </template>
      </el-table-column>

      <!--  -->
      <el-table-column label=""
                       min-width="160">
        <template slot-scope="scope">
          <el-button type="primary"
                     size="mini"
                     plain
                     icon="el-icon-edit"
                     @click="editTag(scope.row)"></el-button>
          <el-button type="danger"
                     size="mini"
                     plain
                     icon="el-icon-delete"
                     style="margin-left:3px"
                     @click="deleteTag(scope.row.id)"></el-button>
          <el-button type="success"
                     size="mini"
                     plain
                     icon="el-icon-top"
                     style="margin-left:3px"
                     :disabled="scope.row.index===1"
                     @click="tagToggle(scope.row.index-1,'up')"></el-button>
          <el-button type="success"
                     size="mini"
                     plain
                     icon="el-icon-bottom"
                     style="margin-left:3px"
                     :disabled="scope.row.index===dataTags.length"
                     @click="tagToggle(scope.row.index-1,'down')"></el-button>
        </template>
      </el-table-column>

    </el-table>

    <!-- dialog  / -->
    <el-dialog class="tag-dialog"
               :title="dialogTitle"
               :visible.sync="dialogVisible">

      <el-form ref="dialogForm"
               :model="formData"
               :rules="formRule"
               label-position="left">

        <el-row :gutter="0">
          <el-col :span="9">
            <el-form-item label-width="65px"
                          label=""
                          prop="name">
              <el-input v-model="formData.name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="9">
            <el-form-item label-width="65px"
                          label=""
                          prop="describe">
              <el-input v-model="formData.describe"></el-input>
            </el-form-item>
          </el-col>
          <el-button style="margin:0 0 20px 10px"
                     @click="setParams"></el-button>
        </el-row>

        <el-row :gutter="0">
          <el-col :span="6">
            <el-form-item label=""
                          prop="dataType">
              <el-select v-model="formData.dataType"
                         placeholder=""
                         style="width:105px">
                <el-option v-for="item in dataTypeList"
                           :key="item"
                           :label="item"
                           :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label=""
                          prop="direction">
              <el-select v-model="formData.direction"
                         placeholder=""
                         style="width:105px">
                <el-option v-for="item in directionList"
                           :key="item"
                           :label="item"
                           :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="9">
            <el-form-item label-width="155px"
                          label=""
                          prop="acquisitionCycle">
              <el-input v-model="formData.acquisitionCycle"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="0">
          <el-col :span="13">
            <el-form-item label-width="100px"
                          label=""
                          prop="registerType">
              <el-select v-model="formData.registerType"
                         placeholder=""
                         style="width:-webkit-fill-available">
                <el-option v-for="item in registerTypeList"
                           :key="item.value"
                           :label="item.label"
                           :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="9">
            <el-form-item label-width="100px"
                          label=""
                          prop="registerAddr">
              <el-input v-model="formData.registerAddr"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="0">
          <el-col :span="10">
            <el-form-item label-width="85px"
                          label=""
                          prop="analyticalMethod">
              <el-select v-model="formData.analyticalMethod"
                         placeholder=""
                         style="width:-webkit-fill-available">
                <el-option v-for="item in analyticalMethodList"
                           :key="item.value"
                           :label="item.label"
                           :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="9">
            <el-form-item label-width="125px"
                          label="0-15"
                          prop="displacementDeviation">
              <el-input v-model="formData.displacementDeviation"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row class="params-dialog-row">
          <el-checkbox v-model="formData.useBCD">BCD</el-checkbox>
        </el-row>

      </el-form>

      <div slot="footer"
           class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button @click="tagSubmit"
                   type="primary"
                   :loading="submitLoading"> </el-button>
      </div>
    </el-dialog>

    <!-- dialog   -->
    <equipment-tag-params ref="equipmentTagParams"
                          :form-data="formData"></equipment-tag-params>

  </div>
</template>

<script>
import { parseTime } from "@/utils"; // functions
import { arraySort } from "@/utils/dataHanding";// function - key
import XLSX from "xlsx"; // plugin - excel
import EquipmentTagParams from "@/components/dialog/equipmentTagParams"; //  - 
/* mockData */
import {
  equipmentTagColumn, //  - 
  equipmentTagHeader, //  - 
  tagTranslation // 
} from "@/mock/tableColumn";

export default {
  components: { EquipmentTagParams },
  props: {
    // id
    id: {
      type: String
    },
    //  - 
    dataTagsOrg: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      /* table */
      dataTags: [], //  - 
      dataColumns: equipmentTagColumn, // 
      dataTypeList: ["", "", "", "", "", ""], // select - 
      dataTypeSelect: "", //  - 
      multipleSelection: [], //  - 
      /* dialog */
      dialogVisible: false, // 
      dialogVisibleParam: false, //  - 
      dialogType: "", // insert/edit
      dialogTitle: "", // dialog
      formData: { // 
        name: "",
        describe: "",
        otherParams: {
          takeAbsoluteValue: false,
          absoluteValue: "",
          calculation: "",
          ratioCalculation: false,
          magnification: "2.0000",
          base: "0.0000",
          reverseCoefficient: false,
          rangeConversion: false,
          maxRange: 10,
          maxGather: 4096,
          minRange: 0,
          minGather: 0,
          dataFilter: false,
          maxData: 100,
          minData: 0,
          dataRadio: "1",
          advancedOperation: "",
          param1: "",
          param2: "",
          code: 0,
          cache: false,
          alert: false,
          maxLimit: "100.0",
          upperLimit: "80.0",
          lowerLimit: "20.0",
          minLimit: "0.0",
          alarmDelay: "3",
          alarmLevel: "3"
        },
        dataType: "",
        direction: "",
        acquisitionCycle: "1000",
        registerType: 0,
        registerAddr: "0",
        analyticalMethod: 0,
        displacementDeviation: "0",
        useBCD: false
      },
      formDataOrg: {}, //  - 
      formRule: { // 
        name: [
          { required: true, message: "", trigger: "blur" }
        ],
        describe: [
          { required: true, message: "", trigger: "blur" }
        ],
        acquisitionCycle: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      directionList: ["", "", ""], // select - 
      registerTypeList: [ // select - 
        {
          label: "15",
          value: 0
        },
        {
          label: "2",
          value: 1
        },
        {
          label: "3616",
          value: 2
        },
        {
          label: "4",
          value: 3
        }
      ],
      analyticalMethodList: [ // select - 
        {
          label: "",
          value: 0
        },
        {
          label: "2  12",
          value: 1
        },
        {
          label: "2 1",
          value: 2
        },
        {
          label: "2 2",
          value: 3
        },
        {
          label: "4 4321",
          value: 4
        },
        {
          label: "8 87654321",
          value: 5
        }
      ],
      /* loading */
      submitLoading: false, // loading - 
      downloadLoading: false, // loading - 
      /*  */
      tableMaxHeight: 0 // 
    };
  },
  created () {
    const screenHeight = document.documentElement.clientHeight;
    this.tableMaxHeight = screenHeight - 73 - 20 * 2 - (40 + 15) - (40 + 20) - 40;
    this.getData();
  },
  methods: {
    // 
    getData () {
      this.refreshData();
    },
    // 
    refreshData () {
      /*  */
      if (this.dataTypeSelect !== "") {
        this.dataTags = this.dataTagsOrg.filter(tag =>
          tag.dataType === this.dataTypeSelect
        );
      } else {
        this.dataTags = this.dataTagsOrg;
      }
      this.dataTags.sort(arraySort("name", "asc")); // 
      this.dataTags.map((tag, i) => {
        this.$set(tag, "index", i + 1); // 1
      });
      // console.log(this.dataTags);
    },
    // 
    insertTag () {
      this.dialogVisible = true;
      this.dialogType = "insert";
      this.$nextTick(() => {
        this.$refs.dialogForm.resetFields();
        this.dialogTitle = `IO-${this.dialogType === "insert" ? "" : ""}`;
      });
    },
    // 
    loadTags () {

    },
    // 
    editTag (row) {
      this.dialogVisible = true;
      this.dialogType = "edit";
      this.$nextTick(() => {
        this.$refs.dialogForm.resetFields();
        this.dialogTitle = `IO-${this.dialogType === "insert" ? "" : ""}`;
        this.formDataOrg = row;
        this.formData = JSON.parse(JSON.stringify(row)); // 
      });
    },
    //  - 
    setParams () {
      this.$refs.equipmentTagParams.setParams();
    },
    // 
    tagSubmit () {
      const formData = JSON.parse(JSON.stringify(this.formData));
      this.$refs.dialogForm.validate(valid => {
        if (valid) {
          this.submitLoading = true;
          switch (this.dialogType) {
            case "insert":
              if (
                this.dataTags.some(
                  tag => tag.name === this.formData.name
                )
              ) {
                // 
                this.$message.error("");
                this.submitLoading = false;
              } else {
                // id
                formData.id = Math.random()
                  .toString(36)
                  .substr(-10);
                this.dataTags.push(JSON.parse(JSON.stringify(formData)));
                this.refreshData();
                this.submitLoading = false;
                this.dialogVisible = false;
              }
              break;
            case "edit":
              if (
                this.dataTags.some(
                  tag => tag.name === this.formData.name
                ) &&
                this.formData.name !== this.formDataOrg.name
              ) {
                // 
                this.$message.error("");
                this.submitLoading = false;
              } else {
                this.$set(
                  this.dataTags,
                  this.formData.index - 1,
                  JSON.parse(JSON.stringify(formData))
                );
                this.submitLoading = false;
                this.dialogVisible = false;
              }
              break;
          }
        }
      });
    },
    // 
    deleteTag (id) {
      this.$confirm(", ?", "", {
        type: "warning"
      }).then(() => {
        this.dataTags.forEach((tag, i) => {
          tag.id === id && this.dataTags.splice(i, 1);
        });
        this.refreshData();
      }).catch(() => { });
    },
    // 
    handleSelectionChange (val) {
      this.multipleSelection = val;
    },
    // 
    deleteTags () {
      if (this.multipleSelection.length === 0) {
        this.$message({
          message: "",
          type: "warning",
          duration: 1000
        });
        return false;
      }
      this.$confirm(", ?", "", {
        type: "warning"
      }).then(() => {
        for (let i = 0; i < this.dataTags.length; i++) {
          this.multipleSelection.forEach(select => {
            this.dataTags[i].id === select.id && this.dataTags.splice(i, 1);
          });
        }
        this.refreshData();
      }).catch(() => { });
    },
    // 
    tagToggle (index, handle) {
      if (handle === "up") {
        // 
        if (index !== 0) {
          const toExchange = this.dataTags[index - 1];
          this.$set(
            this.dataTags,
            index - 1,
            this.dataTags[index]
          );
          this.$set(this.dataTags, index, toExchange);
          this.refreshData();
        }
      } else {
        // 
        if (index !== this.dataTags.length - 1) {
          const toExchange = this.dataTags[index + 1];
          this.$set(
            this.dataTags,
            index + 1,
            this.dataTags[index]
          );
          this.$set(this.dataTags, index, toExchange);
          this.refreshData();
        }
      }
    },
    // 
    downloadTable () {
      this.downloadLoading = true;
      import("@/vendor/Export2Excel").then(excel => {
        const tHeader = equipmentTagHeader.tHeader;
        const filterVal = equipmentTagHeader.filterVal;
        const list = this.dataTags;
        const data = this.formatJson(filterVal, list);
        excel.export_json_to_excel({
          header: tHeader,
          data,
          filename: "-",
          autoWidth: true,
          bookType: "xlsx"
        });
        this.downloadLoading = false;
      });
    },
    // 
    formatJson (filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => {
        if (j === "timestamp") {
          return parseTime(v[j]);
        } else {
          return v[j];
        }
      }));
    },
    // 
    uploadTable () {
      this.$refs["excel-upload-input"].click();
    },
    //  - 
    handleClick (e) {
      const files = e.target.files;
      const rawFile = files[0];
      if (!rawFile) return;
      this.upload(rawFile);
    },
    //  - 
    upload (rawFile) {
      this.$refs["excel-upload-input"].value = null; // fix can't select the same excel
      const before = this.beforeUpload(rawFile);
      if (before) this.readerData(rawFile);
    },
    // 
    beforeUpload (file) {
      const isLt1M = file.size / 1024 / 1024 < 1;
      if (isLt1M) {
        return true;
      }
      this.$message({
        message: "1MB",
        type: "warning"
      });
      return false;
    },
    // 
    readerData (rawFile) {
      this.loading = true;
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = e => {
          const data = e.target.result;
          const workbook = XLSX.read(data, { type: "array" });
          const firstSheetName = workbook.SheetNames[0];
          const worksheet = workbook.Sheets[firstSheetName];
          const header = this.getHeaderRow(worksheet);
          const results = XLSX.utils.sheet_to_json(worksheet);
          // console.log(header);
          // console.log(results);
          this.generateData({ header, results });
          this.loading = false;
          resolve();
        };
        reader.readAsArrayBuffer(rawFile);
      });
    },
    // 
    getHeaderRow (sheet) {
      const headers = [];
      const range = XLSX.utils.decode_range(sheet["!ref"]);
      let C;
      const R = range.s.r;
      /* start in the first row */
      for (C = range.s.c; C <= range.e.c; ++C) { /* walk every column in the range */
        const cell = sheet[XLSX.utils.encode_cell({ c: C, r: R })];
        /* find the cell in the first row */
        let hdr = "UNKNOWN " + C; // <-- replace with your desired default
        if (cell && cell.t) hdr = XLSX.utils.format_cell(cell);
        headers.push(hdr);
      }
      return headers;
    },
    // 
    generateData ({ header, results }) {
      /* 1.resultsdataTags */
      results.forEach(row => {
        Object.keys(row).forEach(key => {
          // console.log(key);
          Object.keys(tagTranslation).forEach(_key => {
            if (key === tagTranslation[_key]) {
              this.$set(row, _key, row[key]);
              this.$delete(row, key);
            }
          });
        });
      });
      console.log(results);
      /* 2.dataTagsOrg */
      this.dataTagsOrg.splice(0, this.dataTagsOrg.length);
      /* 3.resultsdataTagsOrg */
      results.forEach(row => {
        this.$set(row, "otherParams", {});
        this.dataTagsOrg.push(row);
      });
      this.refreshData();
    }
  },
  watch: {
    id (val) {
      this.getData();
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
</style>



