<template>
  <div class="header">
    <div class="btns">
      <el-button size="small"
                 icon="el-icon-plus"
                 type="primary"
                 :disabled="level===3"
                 @click="newBuild"></el-button>
      <el-button size="small"
                 icon="el-icon-close"
                 type="danger"
                 :disabled="level===1"
                 @click="itemDelete"></el-button>
      <el-button size="small"
                 icon="el-icon-caret-right"
                 @click="localOperate"></el-button>
      <el-button size="small"
                 icon="el-icon-suitcase"
                 @click="factoryManage"></el-button>
      <el-button size="small"
                 icon="el-icon-data-board"
                 @click="remoteMonitor"></el-button>
      <el-button size="small"
                 icon="el-icon-download"
                 type="info"></el-button>
      <el-button size="small"
                 icon="el-icon-check"
                 type="success"
                 :disabled="level===1"
                 @click="itemSubmit"></el-button>
      <!-- <el-button size="small"
                 icon="el-icon-refresh"
                 type="warning"></el-button> -->
    </div>

    <!-- dialog -  -->
    <el-dialog class="dispose-dialog"
               :title="dialogDisposeTitle"
               :visible.sync="dialogDisposeVisible">

      <!--  -->
      <el-form v-if="level===1"
               :model="formPass"
               ref="formPass"
               :rules="formPassRule"
               label-position="left">

        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label-width="105px"
                          label="()"
                          prop="passName">
              <el-input v-model="formPass.passName"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label-width="65px"
                          label=""
                          prop="passDescribe">
              <el-input v-model="formPass.passDescribe"></el-input>
            </el-form-item>
          </el-col>
          <el-button style="margin:0 0 20px 5px"
                     @click="setParamsPass"></el-button>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label-width="55px"
                          label="">
              <el-input disabled
                        v-model="formPass.plugin.factory"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label-width="55px"
                          label="">
              <el-input disabled
                        v-model="formPass.plugin.describe"></el-input>
            </el-form-item>
          </el-col>
          <el-button style="margin:0 0 20px 5px"
                     @click="pluginSelect"></el-button>
          <el-button style="margin-bottom:20px"></el-button>
        </el-row>

        <el-row>
          <el-col :span="22">
            <el-form-item label-width="55px"
                          label="">
              <el-input disabled
                        v-model="formPass.plugin.path"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col>
            <el-form-item label-width="85px"
                          label=""
                          prop="passType">
              <el-select v-model="formPass.passType"
                         placeholder="">
                <el-option v-for="item in passTypeList"
                           :key="item"
                           :label="item"
                           :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row v-if="formPass.passType===''"
                :gutter="20">
          <el-col style="width:180px">
            <el-form-item label-width="55px"
                          label=""
                          prop="sata">
              <el-select v-model="formPass.sata"
                         placeholder="">
                <el-option v-for="item in sataList"
                           :key="item"
                           :label="item"
                           :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col style="width:180px">
            <el-form-item label-width="75px"
                          label=""
                          prop="baudRate">
              <el-select v-model="formPass.baudRate"
                         placeholder="">
                <el-option v-for="item in baudList"
                           :key="item"
                           :label="item"
                           :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col style="width:160px">
            <el-form-item label-width="75px"
                          label=""
                          prop="dataBits">
              <el-select v-model="formPass.dataBits"
                         placeholder="">
                <el-option v-for="item in dataList"
                           :key="item"
                           :label="item"
                           :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col style="width:200px">
            <el-form-item label-width="75px"
                          label=""
                          prop="checkBits">
              <el-select v-model="formPass.checkBits"
                         placeholder="">
                <el-option v-for="item in checkList"
                           :key="item"
                           :label="item"
                           :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col style="width:180px">
            <el-form-item label-width="75px"
                          label=""
                          prop="stopBits">
              <el-select v-model="formPass.stopBits"
                         placeholder="">
                <el-option v-for="item in stopList"
                           :key="item"
                           :label="item"
                           :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>

        </el-row>

        <el-row v-if="formPass.passType!=='' && formPass.passType!==''"
                :gutter="20">
          <el-col style="width:300px">
            <el-form-item label-width="70px"
                          :label="formPass.passType==='TCP'?'IP':'IP'"
                          prop="ip">
              <el-input v-model="formPass.ip"></el-input>
            </el-form-item>
          </el-col>
          <el-col style="width:300px">
            <el-form-item label-width="85px"
                          :label="formPass.passType==='TCP'?'':''"
                          prop="port">
              <el-input v-model="formPass.port"></el-input>
            </el-form-item>
          </el-col>
          <el-button v-if="formPass.passType==='TCP'"
                     style="margin-left:20px"
                     @click="bindingIP">IP</el-button>
        </el-row>

        <el-collapse v-if="id&&id.slice(0,1)==='2'"
                     v-model="disposeActiveNames">
          <el-collapse-item title=""
                            name="1">
            <div class="collapse-content">
              <el-input style="max-width:400px"
                        v-model="formPass.delayTime"></el-input>
            </div>
          </el-collapse-item>
        </el-collapse>

      </el-form>

      <!--  -->
      <el-form v-if="level===2"
               :model="formEquipment"
               ref="formEquipment"
               :rules="formEquipmentRule"
               label-position="left">

        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label-width="105px"
                          label="()"
                          prop="equipmentName">
              <el-input v-model="formEquipment.equipmentName"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label-width="65px"
                          label=""
                          prop="equipmentDescribe">
              <el-input v-model="formEquipment.equipmentDescribe"></el-input>
            </el-form-item>
          </el-col>
          <el-button style="margin:0 0 20px 5px"
                     @click="setParamsEquipment"></el-button>
        </el-row>

        <el-row>
          <el-col :span="12">
            <el-form-item label-width="125px"
                          label="(:)"
                          prop="userParam">
              <el-input v-model="formEquipment.userParam"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-collapse v-model="activeNames"
                     accordion>
          <el-collapse-item title=""
                            name="1">
            <div class="collapse-content">MODBUS
              <el-input style="max-width:400px"
                        v-model="formEquipment.MODBUSAdd"></el-input>
            </div>
            <div>
              <el-input style="max-width:400px"
                        v-model="formEquipment.searchNum"></el-input>
            </div>
          </el-collapse-item>
          <el-collapse-item title=""
                            name="2">
            <div class="collapse-content">6
              <el-radio v-model="formEquipment.singleRegister"
                        :label="true">YES</el-radio>
              <el-radio v-model="formEquipment.singleRegister"
                        :label="false">NO</el-radio>
            </div>
            <div>16
              <el-radio v-model="formEquipment.multiRegister"
                        :label="true">YES</el-radio>
              <el-radio v-model="formEquipment.multiRegister"
                        :label="false">NO</el-radio>
            </div>
          </el-collapse-item>
          <el-collapse-item title=""
                            name="3">
            <div class="collapse-content">
              <el-input style="max-width:400px"
                        v-model="formEquipment.correspondingValue"></el-input>
            </div>
            <div class="collapse-content">
              <el-input style="max-width:400px"
                        v-model="formEquipment.subCorrespondingValue"></el-input>
            </div>
            <div>CRC
              <el-radio v-model="formEquipment.doubleByteCheck"
                        :label="true">YES</el-radio>
              <el-radio v-model="formEquipment.doubleByteCheck"
                        :label="false">NO</el-radio>
            </div>
          </el-collapse-item>
        </el-collapse>

      </el-form>

      <div slot="footer"
           class="dialog-footer">
        <el-button @click="dialogDisposeVisible = false"> </el-button>
        <el-button @click="itemAdd"
                   type="primary"> </el-button>
      </div>
    </el-dialog>

    <!-- dialog -    -->
    <pass-params ref="passParams"
                 :id="id"
                 :form-pass="formPass"
                 :pass-type-list="passTypeList"
                 :sata-list="sataList"
                 :baud-list="baudList"
                 :data-list="dataList"
                 :check-list="checkList"
                 :stop-list="stopList"></pass-params>

    <!-- dialog -    -->
    <equipment-params ref="equipmentParams"
                      :id="id"
                      :form-equipment="formEquipment"></equipment-params>

    <!-- dialog -  -->
    <plugin-select ref="pluginSelect"
                   :id="id"
                   :form-pass="formPass"
                   :plugin-list="pluginList"
                   @plugin-click="pluginClick"></plugin-select>

    <!-- dialog -  -->
    <el-dialog class="copy-dialog"
               :title="dialogCopyTitle"
               :visible.sync="dialogCopyVisible">
      <el-table ref="multipleTable"
                :data="dialogCopyData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange">
        <el-table-column type="selection"
                         width="55">
        </el-table-column>
        <el-table-column type="index"
                         label=""
                         width="55">
        </el-table-column>
        <el-table-column :prop="level===1?'passName':'equipmentName'"
                         label=""
                         width="130">
        </el-table-column>
        <el-table-column :prop="level===1?'passDescribe':'equipmentDescribe'"
                         label="">
        </el-table-column>
      </el-table>

      <div slot="footer"
           class="dialog-footer">
        <el-button @click="multipleSubmit('cancel')"> </el-button>
        <el-button @click="multipleSubmit('submit')"
                   type="primary"> </el-button>
      </div>
    </el-dialog>

    <!-- dialog -  -->
    <factory-manage ref="factoryManage"
                    :id-factory="idFactory"
                    :factory-data="factoryData"
                    :pass-list="passList"
                    :equipment-list="equipmentList"
                    @factory-select="factorySelect"></factory-manage>

    <!-- dialog -  -->
    <remote-monitor ref="remoteMonitor"
                    :id-factory="idFactory"
                    :factory-data="factoryData"
                    :tree-data="treeData"></remote-monitor>

    <!-- dialog - IP -->
    <el-dialog class="bind-dialog"
               title="IP"
               :visible.sync="bindingIPVisible">
      <el-row>
        IP
        <el-input v-model="formPass.bindingIp"
                  style="width:300px"></el-input>
      </el-row>
      <el-row style="margin-top:20px">
        IP
      </el-row>

      <div slot="footer"
           class="dialog-footer">
        <el-button @click="bindingIPVisible = false;formPass.bindingIp = bindingIpOrg"> </el-button>
        <el-button @click="bindingIPVisible = false"
                   type="primary"> </el-button>
      </div>

    </el-dialog>

  </div>
</template>

<script>
import PluginSelect from "@/components/dialog/pluginSelect"; // 
import PassParams from "@/components/dialog/passParams"; //  - 
import EquipmentParams from "@/components/dialog/equipmentParams"; //  - 
import FactoryManage from "@/components/dialog/factoryManage"; // 
import RemoteMonitor from "@/components/dialog/remoteMonitor"; // 

export default {
  components: { PluginSelect, PassParams, EquipmentParams, FactoryManage, RemoteMonitor },
  props: {
    // 
    treeData: {
      type: Array
    },
    // 
    factoryData: {
      type: Array
    },
    // add / delete / up / down
    handleType: {
      type: String,
      default: ""
    },
    //  - 
    level: {
      type: Number,
      default: 3
    },
    // id - 
    id: {
      type: String
    },
    // id - 
    idFactory: {
      type: String
    },
    // 
    passList: {
      type: Array
    },
    // 
    pluginList: {
      type: Array
    },
    // 
    passTypeList: {
      type: Array
    },
    // 
    sataList: {
      type: Array
    },
    // 
    baudList: {
      type: Array
    },
    // 
    dataList: {
      type: Array
    },
    // 
    checkList: {
      type: Array
    },
    // 
    stopList: {
      type: Array
    },
    // 
    equipmentList: {
      type: Array
    }
  },
  data () {
    return {
      /* dialog */
      dialogDisposeVisible: false, //  - 
      dialogDisposeTitle: "", //  - 
      dialogCopyTitle: "", //  - 
      dialogCopyVisible: false, //  - 
      dialogCopyData: [], //  - 
      multipleSelection: [], //  - 
      bindingIPVisible: false, // IP - 
      /*  */
      formPass: { // 
        otherParams: {},
        plugin: {}
      },
      formPassRule: { // 
        passName: [
          { required: true, message: "", trigger: "blur" }
        ],
        passDescribe: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      disposeActiveNames: ["1"], // 
      /*  */
      formEquipment: { // 
        otherParams: {}
      },
      formEquipmentRule: { // 
        equipmentName: [
          { required: true, message: "", trigger: "blur" }
        ],
        equipmentDescribe: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      activeNames: ["1"] // 
    };
  },
  watch: {
    handleType (val) {
      // console.log(val);
    }
  },
  methods: {
    //  /
    newBuild (level) {
      this.dialogDisposeVisible = true;
      this.dialogDisposeTitle = `${this.level === 1 ? "" : ""}`;
      // 
      if (this.level === 1) {
        this.formPass = {
          passName: "C1",
          passDescribe: "1",
          plugin: {
            name: "IND_MODBUS_TCP",
            describe: "MODBUS TCP",
            factory: "",
            classification: "",
            path: "C:\\Users\\43577\\Desktop\\\\CESTC\\PluginIo\\IND_MODBUS_RTU",
            plugins: [
              {
                name: "BA_BACNET_IP.335x",
                platform: "335x",
                edition: "5.0.0.1",
                lastModifiedDate: "2017-11-08 07:55:30"
              },
              {
                name: "BA_BACNET_IP.dll",
                platform: "dll",
                edition: "5.0.0.1",
                lastModifiedDate: "2019-02-19 03:45:30"
              },
              {
                name: "BA_BACNET_IP.xt",
                platform: "xt",
                edition: "",
                lastModifiedDate: "2020-02-22 08:47:32"
              }
            ]
          },
          passType: "",
          sata: "COM01",
          baudRate: "9600",
          dataBits: "8",
          checkBits: "",
          stopBits: "1",
          ip: "192.168.0.253",
          port: "50001",
          bindingIp: "192.168.200.161",
          otherParams: {
            scanDelay: false,
            delayTime: 100,
            linkReset: false,
            linkNoDataTime: 60,
            faultDiagnosis: false,
            faultNoDataTime: 60,
            faultShooting: "1",
            packetScanning: "1",
            alternatePass: false,
            passType: "",
            sata: "COM01",
            baudRate: "9600",
            dataBits: "8",
            checkBits: "",
            stopBits: "1",
            ip: "192.168.0.253",
            port: "50001"
          },
          delayTime: 10,
          dataTags: []
        };
      } else {
        this.formEquipment = {
          equipmentName: "D1",
          equipmentDescribe: "1",
          userParam: "",
          MODBUSAdd: "1",
          searchNum: 32,
          singleRegister: true,
          multiRegister: true,
          correspondingValue: "FF00",
          subCorrespondingValue: "0000",
          doubleByteCheck: true,
          otherParams: {
            delayTime: 3000,
            failedTryAgain: false,
            failedTryTimes: 1,
            faultDiagnosis: false,
            continuousQueryFailed: 5,
            noReceivedLongTime: 120,
            faultDataProcess: "GOOD",
            faultScanProcess: "",
            queryPeriod: 30,
            equipmentFactor: false,
            factorR1: "1.000",
            factorR2: "1.000"
          },
          dataTags: []
        };
      }
    },
    //  /
    itemAdd () {
      this.level === 1 && this.$refs.formPass.validate(valid => {
        valid && this.$emit("item-add", this.formPass);
      });
      this.level === 2 && this.$refs.formEquipment.validate(valid => {
        valid && this.$emit("item-add", this.formEquipment);
      });
    },
    // 
    copyItems () {
      this.dialogCopyVisible = true;
      this.dialogCopyTitle = `${this.level === 1 ? "" : ""}-${this.level === 1 ? "" : ""}`;
      /* [/][/] */
      const passSelectList = []; // 
      const equipmentSelectList = []; // 
      this.treeData.forEach(service => {
        service.children.forEach(pass => {
          service.id === this.id && this.passList.forEach(_pass => { // 
            pass.id === _pass.id && passSelectList.push(_pass);
          });
          pass.id === this.id && pass.children.forEach(equipment => { // 
            this.equipmentList.forEach(_equipment => {
              equipment.id === _equipment.id && equipmentSelectList.push(_equipment);
            });
          });
        });
      });
      // console.log(passSelectList);
      // console.log(equipmentSelectList);
      this.dialogCopyData = this.level === 1 ? passSelectList : equipmentSelectList;
    },
    //  - 
    handleSelectionChange (val) {
      this.multipleSelection = val;
    },
    //  /
    multipleSubmit (type) {
      if (type === "cancel") {
        this.$refs.multipleTable.clearSelection();
        this.dialogCopyVisible = false;
      } else {
        this.$emit("items-copy", this.multipleSelection);
        this.dialogCopyVisible = false;
      }
    },
    //  /
    itemDelete () {
      this.$emit("item-delete", this.formPass);
    },
    //  -  - 
    localOperate () {
      this.$confirm("WINDOWS", "", {
        type: "warning"
      }).then(() => {
        this.$refs.remoteMonitor.localOperate();
      }).catch(() => { });
    },
    //  - [] - 
    setParamsPass () {
      this.$refs.passParams.setParams();
    },
    //  -  - 
    pluginSelect () {
      this.$refs.pluginSelect.pluginSelect();
    },
    //  - 
    pluginClick (param) {
      // console.log(param);
      const { level } = param;
      level === 2 && (this.formPass.plugin = param);
      console.log(this.formPass);
    },
    //  - [] - 
    setParamsEquipment () {
      this.$refs.equipmentParams.setParams();
    },
    //  -  - 
    factoryManage () {
      this.$refs.factoryManage.factoryManage();
    },
    // 
    factorySelect (param) {
      this.$emit("factory-select", param);
    },
    //  -  - 
    remoteMonitor () {
      this.$refs.remoteMonitor.ipSelect();
    },
    //  - IP
    bindingIP () {
      this.bindingIPVisible = true;
      this.bindingIpOrg = JSON.parse(JSON.stringify(this.formPass.bindingIp)); // 
    },
    //  - 
    itemSubmit () {
      this.$emit("item-submit", this.level);
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.header /deep/ {
  .el-dialog__body {
    padding: 10px 20px;
  }
  .el-collapse {
    &-item__header {
      font-weight: bold;
    }

    &-item__content {
      padding-bottom: 10px;
    }
    .collapse-content {
      margin-bottom: 10px;
    }
  }
  // dialog
  .dispose-dialog {
    .el-dialog {
      min-width: 1000px;
      .el-row:last-child {
        .el-form-item {
          margin-bottom: 0;
        }
      }
    }
  }
  // dialog
  .params-dialog {
    .el-dialog {
      min-width: 1000px;
    }
    .params-dialog-row:not(:last-child) {
      margin-bottom: 10px;
      .el-checkbox {
        margin-bottom: 10px;
      }
    }
    &-row {
      &-div {
        margin-left: 24px;
      }
      &-select {
        margin-bottom: 10px;
      }
    }
  }
  // dialog
  .copy-dialog {
    .el-dialog {
      max-width: 700px;
      min-width: 500px;
    }
  }
  // dialog
  .plugin-dialog {
    .el-dialog {
      min-width: 1000px;

      .el-form {
        margin: 10px 20px;

        &-item {
          margin-bottom: 10px;
        }
      }
    }
  }
  // dialog
  .factory-manage {
    .el-dialog {
      width: 320px;
      min-height: 500px;
      .btns {
        margin-bottom: 20px;
      }
    }
  }
  // /dialog
  .factory-operate {
    .el-dialog {
      width: 400px;
    }
  }
  // ipdialog
  .ip-select {
    .el-dialog {
      min-width: 1000px;
      min-height: 500px;
      .el-table {
        tr {
          cursor: pointer;
          .el-icon-check {
            color: #67c23a;
            border: 1px solid #67c23a;
          }
          .el-icon-close {
            color: #f56c6c;
            border: 1px solid #f56c6c;
          }
        }
      }
    }
  }
  // dialog
  .monitor-dialog {
    .el-dialog {
      width: 80%;
      min-width: 1200px;
      .el-form {
        margin: 10px 20px;

        .el-table {
          min-height: 500px;
          .cell {
            padding: 0 5px;
          }
        }
      }
    }
  }
  // dialog - ip
  .bind-dialog {
    .el-dialog {
      width: 450px;
    }
  }
}
</style>



