<template>
  <el-dialog class="params-dialog"
             :title="dialogTitle"
             :visible.sync="dialogVisible">

    <el-row class="params-dialog-row">
      <el-checkbox v-model="formPass.otherParams.scanDelay"></el-checkbox>
      <div class="params-dialog-row-div">
        
        <el-input v-model="formPass.otherParams.delayTime"
                  style="width:100px"
                  :disabled="!formPass.otherParams.scanDelay"></el-input>
        <el-popover placement="top-start"
                    title=""
                    width="200"
                    trigger="hover"
                    content=""
                    style="margin-left:5px">
          <el-button slot="reference"
                     type="primary"
                     plain>?</el-button>
        </el-popover>
      </div>
    </el-row>

    <el-row v-if="id&&id.slice(0,1)==='1'"
            class="params-dialog-row">
      <el-checkbox v-model="formPass.otherParams.linkReset"></el-checkbox>
      <div class="params-dialog-row-div">
        
        <el-input v-model="formPass.otherParams.linkNoDataTime"
                  style="width:100px"
                  :disabled="!formPass.otherParams.linkReset"></el-input>
        &nbsp;>=60
      </div>
    </el-row>

    <el-row class="params-dialog-row">
      <el-checkbox v-model="formPass.otherParams.faultDiagnosis"
                   style="margin-bottom:10px"></el-checkbox>
      <div class="params-dialog-row-div">
        
        <el-input v-model="formPass.otherParams.faultNoDataTime"
                  style="width:100px"
                  :disabled="!formPass.otherParams.faultDiagnosis"></el-input>
        &nbsp;{{id&&id.slice(0,1)==='1'?'':''}}>={{id&&id.slice(0,1)==='1'?'5':'120'}}
        <div v-if="id&&id.slice(0,1)==='2'"
             style="margin:10px 0">
          
          <el-radio v-model="formPass.otherParams.faultShooting"
                    label="1"
                    :disabled="!formPass.otherParams.faultDiagnosis"></el-radio>
          <el-radio v-model="formPass.otherParams.faultShooting"
                    label="2"
                    :disabled="!formPass.otherParams.faultDiagnosis"></el-radio>
        </div>
      </div>
    </el-row>

    <el-row v-if="id&&id.slice(0,1)==='1'"
            class="params-dialog-row">
      
      <div class="params-dialog-row-div"
           style="margin-top:10px">
        <el-radio v-model="formPass.otherParams.packetScanning"
                  label="1"
                  disabled></el-radio>
        <el-radio v-model="formPass.otherParams.packetScanning"
                  label="2"
                  disabled></el-radio>
      </div>
    </el-row>

    <el-row v-if="id&&id.slice(0,1)==='1'"
            class="params-dialog-row">
      <el-checkbox v-model="formPass.otherParams.alternatePass"></el-checkbox>
    </el-row>

    <div v-if="id&&id.slice(0,1)==='1'&&formPass.otherParams.alternatePass">
      <el-row class="params-dialog-row">
        <el-col>
          
          <el-select v-model="formPass.otherParams.passType"
                     placeholder="">
            <el-option v-for="item in passTypeList"
                       :key="item"
                       :label="item"
                       :value="item">
            </el-option>
          </el-select>
        </el-col>
      </el-row>

      <el-row class="params-dialog-row"
              v-if="formPass.otherParams.passType===''"
              :gutter="20">
        <el-col :span="8"
                class="params-dialog-row-select">
          
          <el-select v-model="formPass.otherParams.sata"
                     placeholder="">
            <el-option v-for="item in sataList"
                       :key="item"
                       :label="item"
                       :value="item">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="8"
                class="params-dialog-row-select">
          
          <el-select v-model="formPass.otherParams.baudRate"
                     placeholder="">
            <el-option v-for="item in baudList"
                       :key="item"
                       :label="item"
                       :value="item">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="8"
                class="params-dialog-row-select">
          
          <el-select v-model="formPass.otherParams.dataBits"
                     placeholder="">
            <el-option v-for="item in dataList"
                       :key="item"
                       :label="item"
                       :value="item">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="8">
          
          <el-select v-model="formPass.otherParams.checkBits"
                     placeholder="">
            <el-option v-for="item in checkList"
                       :key="item"
                       :label="item"
                       :value="item">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="8">
          
          <el-select v-model="formPass.otherParams.stopBits"
                     placeholder="">
            <el-option v-for="item in stopList"
                       :key="item"
                       :label="item"
                       :value="item">
            </el-option>
          </el-select>
        </el-col>

      </el-row>

      <el-row class="params-dialog-row"
              v-if="formPass.otherParams.passType!=='' && formPass.otherParams.passType!==''"
              :gutter="20">
        <el-col>
          {{formPass.otherParams.passType==='TCP'?'IP':'IP'}}
          <el-input v-model="formPass.otherParams.ip"
                    style="width:150px;margin-right:10px"></el-input>
          {{formPass.otherParams.passType==='TCP'?'':''}}
          <el-input v-model="formPass.otherParams.port"
                    style="width:100px;margin-right:10px"></el-input>
        </el-col>
      </el-row>
    </div>

    <div slot="footer"
         class="dialog-footer">
      <el-button @click="dialogVisible = false;formPass.otherParams = paramsOrg"> </el-button>
      <el-button @click="dialogVisible = false"
                 type="primary"> </el-button>
    </div>
  </el-dialog>
</template>

<script>

export default {
  props: {
    // id
    id: {
      type: String
    },
    // 
    formPass: {
      type: Object
    },
    // 
    passTypeList: {
      type: Array
    },
    // 
    sataList: {
      type: Array
    },
    // 
    baudList: {
      type: Array
    },
    // 
    dataList: {
      type: Array
    },
    // 
    checkList: {
      type: Array
    },
    // 
    stopList: {
      type: Array
    }
  },
  data () {
    return {
      dialogVisible: false, // 
      dialogTitle: "" // 
    };
  },
  methods: {
    // 
    setParams () {
      this.dialogVisible = true;
      this.paramsOrg = JSON.parse(JSON.stringify(this.formPass.otherParams)); // 
      this.dialogTitle = `${this.id.slice(0, 1) === "1" ? "" : ""} `;
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
</style>



