<template>
  <div>

    <!-- ip -->
    <el-dialog class="ip-select"
               title="IP"
               :visible.sync="dialogVisibleIP">

      <!-- btns -->
      <el-row style="margin-bottom:20px">
        <el-button type="warning"
                   size="small"
                   plain
                   @click="searchEquipment"></el-button>
        <el-button type="primary"
                   size="small"
                   plain
                   @click="insertIP"></el-button>
        <el-button type="danger"
                   size="small"
                   plain
                   @click="deleteIPs"></el-button>
        <el-button type="success"
                   size="small"
                   plain
                   @click="updateStatus"></el-button>
      </el-row>

      <el-table :data="IPList"
                tooltip-effect="dark"
                :max-height="tableMaxHeight"
                @row-dblclick="remoteMonitor"
                @selection-change="handleSelectionChange">
        <el-table-column type="selection"
                         width="30">
        </el-table-column>
        <el-table-column label=""
                         width="30">
          <template slot-scope="scope">
            <i :class="scope.row.operate?'el-icon-check':'el-icon-close'"></i>
          </template>
        </el-table-column>
        <el-table-column type="index"
                         label=""
                         width="50">
        </el-table-column>
        <el-table-column prop="address"
                         label="IP"
                         min-width="100">
        </el-table-column>
        <el-table-column prop="system"
                         label=""
                         min-width="100">
        </el-table-column>
        <el-table-column prop="productName"
                         label=""
                         min-width="80">
        </el-table-column>
        <el-table-column prop="factoryDescribe"
                         label=""
                         min-width="120">
        </el-table-column>
        <el-table-column prop="kernelEdition"
                         label=""
                         min-width="80">
        </el-table-column>
        <el-table-column prop="faultTimes"
                         label=""
                         min-width="80">
        </el-table-column>
        <el-table-column prop="createTime"
                         label=""
                         min-width="140">
        </el-table-column>
        <el-table-column prop="status"
                         label=""
                         min-width="60">
        </el-table-column>
      </el-table>
    </el-dialog>

    <!-- ipInsert -->
    <el-dialog class="factory-operate"
               title="IP"
               :visible.sync="dialogVisibleIPInsert">

      <el-form ref="IPform"
               :model="IPform"
               label-width="90px"
               label-position="right">

        <el-form-item prop="address"
                      label="IP">
          <el-input v-model="IPform.address"></el-input>
        </el-form-item>
        <el-form-item prop="factoryDescribe"
                      label="">
          <el-input v-model="IPform.factoryDescribe"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer"
           class="dialog-footer">
        <el-button @click="dialogVisibleIPInsert = false"> </el-button>
        <el-button @click="submitIP"
                   type="primary"
                   :loading="submitLoading"> </el-button>
      </div>

    </el-dialog>

    <!-- monitor -->
    <el-dialog class="monitor-dialog"
               :title="dialogTitleMonitor"
               :visible.sync="dialogVisibleMonitor"
               top="10vh">

      <el-container>

        <!--  -->
        <el-aside class="left-panel">
          <left-tree class="left-panel-tree"
                     :data="monitorTree"
                     :contextmenu="false"
                     :collapse="false"
                     @item-click="itemClick">
          </left-tree>
        </el-aside>

        <!--  -->
        <el-container class="right-panel is-vertical">
          <el-form label-position="left">
            <el-table :data="monitorTable"
                      tooltip-effect="dark"
                      empty-text=" "
                      :max-height="tableMaxHeight">
              <el-table-column type="index"
                               label=""
                               width="40">
              </el-table-column>
              <el-table-column prop="name"
                               label="()"
                               min-width="150">
              </el-table-column>
              <el-table-column prop="describe"
                               label="()"
                               min-width="160">
              </el-table-column>
              <el-table-column prop="curValue"
                               label=""
                               min-width="180">
              </el-table-column>
              <el-table-column prop="curTime"
                               label=""
                               min-width="160">
              </el-table-column>
              <el-table-column prop="qualityStamp"
                               label=""
                               min-width="80">
              </el-table-column>
              <el-table-column prop="changeTimes"
                               label=""
                               min-width="80">
              </el-table-column>
              <el-table-column prop="handle"
                               label="HANDLE"
                               min-width="80">
              </el-table-column>
              <el-table-column prop="dataType"
                               label=""
                               min-width="80">
              </el-table-column>
              <el-table-column prop="direction"
                               label=""
                               min-width="80">
              </el-table-column>
            </el-table>
          </el-form>
        </el-container>

      </el-container>

    </el-dialog>
  </div>
</template>

<script>
import LeftTree from "@/components/Tree"; // 
import { treeTempleteData } from "@/mock/tree"; // mockData - // 
import { IPList, systemDic, passDic, equipmentDic } from "@/mock/dictionary"; // mockData - IP&
import { parseTime } from "@/utils"; // function - 

export default {
  components: { LeftTree },
  props: {
    // 
    factoryData: {
      type: Array,
      default: () => []
    },
    // id - 
    idFactory: {
      type: String
    },
    // 
    treeData: {
      type: Array
    }
  },
  data () {
    return {
      operate: false, // 
      ipOperate: false, // IP
      /* ip */
      dialogVisibleIP: false, //  - ip
      dialogVisibleIPInsert: false, //  - ip
      IPList: [], // ip
      multipleSelection: [], //  - 
      IPform: { // ip
        address: "192.168.0.253",
        system: "",
        productName: "",
        factoryDescribe: "",
        kernelEdition: "",
        faultTimes: null,
        createTime: "",
        status: "",
        operate: false
      },
      /* monitor */
      dialogVisibleMonitor: false, //  - 
      dialogTitleMonitor: "", //  - 
      monitorTree: [], //  - 
      monitorTable: [], //  - 
      handle: 0, // handle
      /*  */
      tableMaxHeight: 0, // 
      /* loading */
      submitLoading: false
    };
  },
  created () {
    const screenHeight = document.documentElement.clientHeight;
    this.tableMaxHeight = screenHeight - 54 - 10 * 2 - 10 * 2 - screenHeight * 0.20;
  },
  methods: {
    // ip - 
    ipSelect () {
      this.dialogVisibleIP = true;
      this.IPList = IPList;
      this.refreshIPTable(); // ip
    },
    // ip - 
    refreshIPTable () {
      if (this.operate) {
        this.$set(this.IPList[0], "system", "windows");
        this.$set(this.IPList[0], "productName", "");
        this.$set(this.IPList[0], "kernelEdition", "5");
        this.$set(this.IPList[0], "faultTimes", 0);
        this.factoryData[0].children.forEach(group => {
          group.children.forEach(factory => {
            if (factory.id === this.idFactory) {
              this.$set(this.IPList[0], "factoryDescribe", factory.text);
              this.$set(this.IPList[0], "createTime", factory.creatTime);
            }
          });
        });
        this.$set(this.IPList[0], "status", "");
        this.$set(this.IPList[0], "operate", true);
      }
    },
    // ip - 
    handleSelectionChange (val) {
      this.multipleSelection = val;
    },
    // ip - 
    insertIP () {
      this.dialogVisibleIPInsert = true;
      this.$nextTick(() => {
        this.$refs.IPform.resetFields();
      });
    },
    // ip - 
    submitIP () {
      const formData = JSON.parse(JSON.stringify(this.IPform));
      // console.log(formData);
      this.submitLoading = true;
      if (IPList.some(ip => ip.address === this.IPform.address)) { // 
        this.$message.error("IP");
        this.submitLoading = false;
      } else {
        formData.id = Math.random().toString(36).substr(-10);// id
        IPList.push(formData);
        this.submitLoading = false;
        this.dialogVisibleIPInsert = false;
      }
    },
    // ip - 
    deleteIPs () {
      if (this.multipleSelection.length === 0) {
        this.$message({
          message: "IP",
          type: "warning",
          duration: 1000
        });
        return false;
      }
      this.$confirm("IP, ?", "", {
        type: "warning"
      }).then(() => {
        for (let i = 0; i < this.IPList.length; i++) {
          this.multipleSelection.forEach(select => {
            this.IPList[i].id === select.id && this.IPList.splice(i, 1);
          });
        }
      }).catch(() => { });
    },
    // ip - 
    localOperate () {
      this.operate = true;
    },
    // ip - 
    searchEquipment () {
      this.refreshIPTable(); // ip
    },
    // ip - 
    updateStatus () {
      this.refreshIPTable(); // ip
    },
    //  - 
    remoteMonitor (row) {
      this.handle = 0;
      this.ipOperate = row.operate;
      this.dialogTitleMonitor = row.address;
      this.dialogVisibleMonitor = true;
      this.$nextTick(() => {
        this.getTreeData(); // 
        this.getTableData(this.monitorTree[0].tableData); // 
      });
    },
    //  - 
    getTreeData () {
      const treeData = JSON.parse(JSON.stringify(this.treeData)); // 
      this.monitorTree = [
        {
          text: "",
          icon: "fa fa-folder",
          id: "root",
          level: 0,
          selected: true,
          opened: true,
          children: this.operate && this.ipOperate ? treeData : treeTempleteData
        }
      ];
      this.handleRecursion(this.monitorTree); // 
    },
    //  - 
    getTableData (data) {
      this.monitorTable = this.operate && this.ipOperate ? data : []; // 
      this.monitorTable.forEach((item, i) => { // 
        this.$set(item, "curTime", parseTime(new Date()));
        this.$set(item, "qualityStamp", "good");
        this.$set(item, "changeTimes", Math.floor(Math.random() * (1 - 101) + 101));
        this.$set(item, "direction", "");
        item.name === "_project_name" && this.factoryData[0].children.forEach(group => {
          group.children.forEach(factory => {
            factory.id === this.idFactory && this.$set(item, "curValue", factory.text);
          });
        });
        item.name === "_project_create_time" && this.factoryData[0].children.forEach(group => {
          group.children.forEach(factory => {
            factory.id === this.idFactory && this.$set(item, "curValue", factory.creatTime);
          });
        });
      });
    },
    //  - 
    itemClick (param) {
      console.log(param);
      this.getTableData(param.tableData); // 
    },
    //  -  - 
    handleRecursion (recursionData) {
      recursionData.forEach(item => {
        const params = {
          opened: true,
          selected: item.level === 0,
          tableData:
            item.level === 0 ? JSON.parse(JSON.stringify(systemDic))
              : item.level === 1 ? []
                : item.level === 3 ? JSON.parse(JSON.stringify(equipmentDic))
                  : item.id.slice(0, 1) === "1"
                    ? JSON.parse(JSON.stringify(passDic.filter(pass => pass.type.indexOf("collect") > -1)))
                    : JSON.parse(JSON.stringify(passDic.filter(pass => pass.type.indexOf("data") > -1)))
        };
        this.dataPackage(item, params);
        item.children && this.handleRecursion(item.children);
      });
    },
    //  - 
    dataPackage (item, params) {
      Object.keys(params).forEach(key => {
        this.$set(item, key, params[key]); //   tableData
      });
      item.tableData.forEach(_item => { // handle
        this.$set(_item, "handle", this.handle);
        this.handle++;
      });
    }

  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
</style>



