<template>
  <el-dialog class="plugin-dialog"
             v-if="dialogVisible"
             :title="dialogTitle"
             :visible.sync="dialogVisible">

    <el-container>

      <!--  -->
      <el-aside class="left-panel">
        <left-tree class="left-panel-tree"
                   :data="pluginList"
                   :contextmenu="false"
                   :collapse="false"
                   @item-click="pluginClick">
        </left-tree>
      </el-aside>

      <!--  -->
      <el-container class="right-panel is-vertical">
        <el-form label-position="left">

          <el-row :gutter="10">
            <el-col :span="11">
              <el-form-item label-width="55px"
                            label="">
                <el-input disabled
                          v-model="formPass.plugin.name"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="11">
              <el-form-item label-width="55px"
                            label="">
                <el-input disabled
                          v-model="formPass.plugin.describe"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="11">
              <el-form-item label-width="55px"
                            label="">
                <el-input disabled
                          v-model="formPass.plugin.classification"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="11">
              <el-form-item label-width="55px"
                            label="">
                <el-input disabled
                          v-model="formPass.plugin.factory"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-table :data="formPass.plugin.plugins"
                    tooltip-effect="dark">
            <el-table-column type="index"
                             label=""
                             width="55">
            </el-table-column>
            <el-table-column prop="name"
                             label=""
                             width="200">
            </el-table-column>
            <el-table-column prop="platform"
                             label=""
                             width="120">
            </el-table-column>
            <el-table-column prop="edition"
                             label=""
                             width="120">
            </el-table-column>
            <el-table-column prop="lastModifiedDate"
                             label=""
                             width="160">
            </el-table-column>
          </el-table>

        </el-form>
      </el-container>

    </el-container>

    <div slot="footer"
         class="dialog-footer">
      <el-button @click="dialogVisible = false;formPass.plugin = pluginOrg;"> </el-button>
      <el-button type="primary"
                 @click="dialogVisible = false"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import LeftTree from "@/components/Tree"; // 

export default {
  components: { LeftTree },
  props: {
    // id
    id: {
      type: String
    },
    // 
    formPass: {
      type: Object,
      default: () => { }
    },
    // 
    pluginList: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      dialogVisible: false, // 
      dialogTitle: "" // 
    };
  },
  methods: {
    // 
    pluginSelect () {
      this.dialogVisible = true;
      this.pluginOrg = JSON.parse(JSON.stringify(this.formPass.plugin)); // 
      this.dialogTitle = `-${this.id.slice(0, 1) === "1" ? "" : ""}`;
    },
    //  - 
    pluginClick (param) {
      this.$emit("plugin-click", param);
    }
  },
  watch: {
    dialogVisible (val) {
      val === true && this.pluginList.forEach(classification => {
        /* 1. */
        classification.opened = false; // 
        this.formPass.plugin.classification === classification.text &&
          (classification.opened = true); // 
        /* 2. */
        classification.children.forEach(plugin => {
          plugin.selected = false; // 
          this.formPass.plugin.name === plugin.name &&
            (plugin.selected = true); // 
        });
      });
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
</style>



