<template>
  <div class="content">

    <!--  -->
    <el-form v-if="id.slice(0,1)==='1'"
             :model="formPass"
             ref="formPass"
             :rules="formPassRule"
             label-position="left">

      <el-row>
        <el-col style="width:420px">
          <el-form-item label-width="105px"
                        label="()"
                        prop="passName">
            <el-input v-model="formPass.passName"></el-input>
          </el-form-item>
        </el-col>
        <el-col style="width:380px">
          <el-form-item label-width="65px"
                        label=""
                        prop="passDescribe">
            <el-input v-model="formPass.passDescribe"></el-input>
          </el-form-item>
        </el-col>
        <el-button style="margin:0 0 20px 20px"
                   @click="setParams"></el-button>
      </el-row>

      <el-row>
        <el-col style="width:358px">
          <el-form-item label-width="55px"
                        label="">
            <el-input disabled
                      v-model="formPass.plugin.factory"></el-input>
          </el-form-item>
        </el-col>
        <el-col style="width:358px">
          <el-form-item label-width="55px"
                        label="">
            <el-input disabled
                      v-model="formPass.plugin.describe"></el-input>
          </el-form-item>
        </el-col>
        <el-button style="margin:0 0 20px 20px"
                   @click="pluginSelect"></el-button>
        <el-button style="margin-bottom:20px"></el-button>
      </el-row>

      <el-row>
        <el-col style="width:940px">
          <el-form-item label-width="55px"
                        label="">
            <el-input disabled
                      v-model="formPass.plugin.path"></el-input>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row>
        <el-col>
          <el-form-item label-width="85px"
                        label=""
                        prop="passType">
            <el-select v-model="formPass.passType"
                       placeholder="">
              <el-option v-for="item in passTypeList"
                         :key="item"
                         :label="item"
                         :value="item">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row v-if="formPass.passType===''">
        <el-col style="width:160px">
          <el-form-item label-width="55px"
                        label=""
                        prop="sata">
            <el-select v-model="formPass.sata"
                       placeholder="">
              <el-option v-for="item in sataList"
                         :key="item"
                         :label="item"
                         :value="item">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col style="width:180px">
          <el-form-item label-width="75px"
                        label=""
                        prop="baudRate">
            <el-select v-model="formPass.baudRate"
                       placeholder="">
              <el-option v-for="item in baudList"
                         :key="item"
                         :label="item"
                         :value="item">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col style="width:160px">
          <el-form-item label-width="75px"
                        label=""
                        prop="dataBits">
            <el-select v-model="formPass.dataBits"
                       placeholder="">
              <el-option v-for="item in dataList"
                         :key="item"
                         :label="item"
                         :value="item">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col style="width:180px">
          <el-form-item label-width="75px"
                        label=""
                        prop="checkBits">
            <el-select v-model="formPass.checkBits"
                       placeholder="">
              <el-option v-for="item in checkList"
                         :key="item"
                         :label="item"
                         :value="item">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col style="width:180px">
          <el-form-item label-width="75px"
                        label=""
                        prop="stopBits">
            <el-select v-model="formPass.stopBits"
                       placeholder="">
              <el-option v-for="item in stopList"
                         :key="item"
                         :label="item"
                         :value="item">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>

      </el-row>

      <el-row v-if="formPass.passType!=='' && formPass.passType!==''">
        <el-col style="width:300px">
          <el-form-item label-width="70px"
                        :label="formPass.passType==='TCP'?'IP':'IP'"
                        prop="ip">
            <el-input v-model="formPass.ip"></el-input>
          </el-form-item>
        </el-col>
        <el-col style="width:300px">
          <el-form-item label-width="85px"
                        :label="formPass.passType==='TCP'?'':''"
                        prop="port">
            <el-input v-model="formPass.port"></el-input>
          </el-form-item>
        </el-col>
        <el-button v-if="formPass.passType==='TCP'"
                   style="margin-left:20px"
                   @click="bindingIP">IP</el-button>
      </el-row>

    </el-form>

    <!--  -->
    <el-tabs v-else
             v-model="activeName">

      <!--  -->
      <el-tab-pane name="first">
        <span slot="label">
          <i class="el-icon-setting"></i> 
        </span>
        <el-form :model="formPass"
                 ref="formPass"
                 :rules="formPassRule"
                 label-position="left">

          <el-row>
            <el-col style="width:420px">
              <el-form-item label-width="105px"
                            label="()"
                            prop="passName">
                <el-input v-model="formPass.passName"></el-input>
              </el-form-item>
            </el-col>
            <el-col style="width:380px">
              <el-form-item label-width="65px"
                            label=""
                            prop="passDescribe">
                <el-input v-model="formPass.passDescribe"></el-input>
              </el-form-item>
            </el-col>
            <el-button style="margin:0 0 20px 20px"
                       @click="setParams"></el-button>
          </el-row>

          <el-row>
            <el-col style="width:358px">
              <el-form-item label-width="55px"
                            label="">
                <el-input disabled
                          v-model="formPass.plugin.factory"></el-input>
              </el-form-item>
            </el-col>
            <el-col style="width:358px">
              <el-form-item label-width="55px"
                            label="">
                <el-input disabled
                          v-model="formPass.plugin.describe"></el-input>
              </el-form-item>
            </el-col>
            <el-button style="margin:0 0 20px 20px"
                       @click="pluginSelect"></el-button>
            <el-button style="margin-bottom:20px"></el-button>
          </el-row>

          <el-row>
            <el-col style="width:940px">
              <el-form-item label-width="55px"
                            label="">
                <el-input disabled
                          v-model="formPass.plugin.path"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row>
            <el-col>
              <el-form-item label-width="85px"
                            label=""
                            prop="passType">
                <el-select v-model="formPass.passType"
                           placeholder="">
                  <el-option v-for="item in passTypeList"
                             :key="item"
                             :label="item"
                             :value="item">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row v-if="formPass.passType===''">
            <el-col style="width:160px">
              <el-form-item label-width="55px"
                            label=""
                            prop="sata">
                <el-select v-model="formPass.sata"
                           placeholder="">
                  <el-option v-for="item in sataList"
                             :key="item"
                             :label="item"
                             :value="item">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col style="width:180px">
              <el-form-item label-width="75px"
                            label=""
                            prop="baudRate">
                <el-select v-model="formPass.baudRate"
                           placeholder="">
                  <el-option v-for="item in baudList"
                             :key="item"
                             :label="item"
                             :value="item">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col style="width:160px">
              <el-form-item label-width="75px"
                            label=""
                            prop="dataBits">
                <el-select v-model="formPass.dataBits"
                           placeholder="">
                  <el-option v-for="item in dataList"
                             :key="item"
                             :label="item"
                             :value="item">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col style="width:180px">
              <el-form-item label-width="75px"
                            label=""
                            prop="checkBits">
                <el-select v-model="formPass.checkBits"
                           placeholder="">
                  <el-option v-for="item in checkList"
                             :key="item"
                             :label="item"
                             :value="item">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col style="width:180px">
              <el-form-item label-width="75px"
                            label=""
                            prop="stopBits">
                <el-select v-model="formPass.stopBits"
                           placeholder="">
                  <el-option v-for="item in stopList"
                             :key="item"
                             :label="item"
                             :value="item">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

          </el-row>

          <el-row v-if="formPass.passType!=='' && formPass.passType!==''">
            <el-col style="width:300px">
              <el-form-item label-width="70px"
                            :label="formPass.passType==='TCP'?'IP':'IP'"
                            prop="ip">
                <el-input v-model="formPass.ip"></el-input>
              </el-form-item>
            </el-col>
            <el-col style="width:300px">
              <el-form-item label-width="85px"
                            :label="formPass.passType==='TCP'?'':''"
                            prop="port">
                <el-input v-model="formPass.port"></el-input>
              </el-form-item>
            </el-col>
            <el-button v-if="formPass.passType==='TCP'"
                       style="margin-left:20px"
                       @click="bindingIP">IP</el-button>
            <el-button v-if="formPass.passType==='TCP'"
                       style="margin-left:20px"></el-button>
          </el-row>

          <el-collapse v-model="activeNames">
            <el-collapse-item title=""
                              name="1">
              <div>{{formPass.delayTime}}</div>
            </el-collapse-item>
          </el-collapse>

        </el-form>
      </el-tab-pane>

      <!--  -->
      <el-tab-pane name="second">
        <span slot="label">
          <i class="el-icon-edit-outline"></i> 
        </span>

        <!-- table -    -->
        <pass-tags :id="id"
                   :tree-data="treeData"
                   :pass-list="passList"
                   :equipment-list="equipmentList"
                   :data-tags-org="formPass.dataTags"></pass-tags>

      </el-tab-pane>

    </el-tabs>

    <!-- dialog -    -->
    <pass-params ref="passParams"
                 :id="id"
                 :form-pass="formPass"
                 :pass-type-list="passTypeList"
                 :sata-list="sataList"
                 :baud-list="baudList"
                 :data-list="dataList"
                 :check-list="checkList"
                 :stop-list="stopList"></pass-params>

    <!-- dialog -  -->
    <plugin-select ref="pluginSelect"
                   :id="id"
                   :form-pass="formPass"
                   :plugin-list="pluginList"
                   @plugin-click="pluginClick"></plugin-select>

    <!-- dialog - IP -->
    <el-dialog class="bind-dialog"
               title="IP"
               :visible.sync="bindingIPVisible">
      <el-row>
        IP
        <el-input v-model="formPass.bindingIp"
                  style="width:300px"></el-input>
      </el-row>
      <el-row style="margin-top:20px">
        IP
      </el-row>

      <div slot="footer"
           class="dialog-footer">
        <el-button @click="bindingIPVisible = false;formPass.bindingIp = bindingIpOrg"> </el-button>
        <el-button @click="bindingIPVisible = false"
                   type="primary"> </el-button>
      </div>

    </el-dialog>

  </div>
</template>

<script>
import PluginSelect from "@/components/dialog/pluginSelect"; // 
import PassParams from "@/components/dialog/passParams"; //  - 
import PassTags from "@/components/table/passTags"; //  - 

export default {
  components: { PluginSelect, PassParams, PassTags },
  props: {
    //  - tagSelect
    treeData: {
      type: Array,
      default: () => []
    },
    //  - tagSelect
    equipmentList: {
      type: Array,
      default: () => []
    },
    // id
    id: {
      type: String
    },
    // 
    passList: {
      type: Array,
      default: () => []
    },
    // 
    pluginList: {
      type: Array,
      default: () => []
    },
    // 
    passTypeList: {
      type: Array
    },
    // 
    sataList: {
      type: Array
    },
    // 
    baudList: {
      type: Array
    },
    // 
    dataList: {
      type: Array
    },
    // 
    checkList: {
      type: Array
    },
    // 
    stopList: {
      type: Array
    }
  },
  data () {
    return {
      formPass: {}, // 
      formPassRule: { // 
        passName: [
          { required: true, message: "", trigger: "blur" }
        ],
        passDescribe: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      activeName: "first", // tabs
      activeNames: ["1"], // 
      /* dialog */
      bindingIPVisible: false // IP - 
    };
  },
  created () {
    this.getData();
  },
  methods: {
    // 
    getData () {
      this.passList.forEach((pass, i) => {
        pass.id === this.id &&
          (this.formPass = JSON.parse(JSON.stringify(this.passList[i])));
      });
      this.activeName = "first"; // tab
    },
    //  -  - 
    setParams () {
      this.$refs.passParams.setParams();
    },
    //  -  - 
    pluginSelect () {
      this.$refs.pluginSelect.pluginSelect();
    },
    //  - 
    pluginClick (param) {
      // console.log(param);
      const { level } = param;
      level === 2 && (this.formPass.plugin = param);
      // console.log(this.formPass);
    },
    //  - IP
    bindingIP () {
      this.bindingIPVisible = true;
      this.bindingIpOrg = JSON.parse(JSON.stringify(this.formPass.bindingIp)); // 
    },
    // 
    passSubmit () {
      this.$refs.formPass.validate(valid => {
        if (valid) {
          this.passList.forEach((pass, i) => { // 
            pass.id === this.id &&
              this.$set(this.passList, i, this.formPass);
          });
          this.treeData.forEach(service => { // 
            service.children.forEach((pass, i) => {
              pass.id === this.id &&
                this.$set(pass, "text",
                  `${this.formPass.passName}[${this.formPass.passDescribe}]`
                );
            });
          });
          this.$message({
            message: "",
            type: "success",
            duration: "1000"
          });
        }
      });
    }
  },
  watch: {
    id (val) {
      this.getData();
      // console.log(this.formPass);
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
@import '~@/styles/content.scss';
</style>



