<template>
  <div class="table-component">

    <!-- btns -->
    <el-row style="margin-bottom:20px">
      <el-select v-model="dataTypeSelect"
                 placeholder=""
                 style="width:150px;margin-right:10px"
                 @change="refreshData">
        <el-option v-for="item in dataTypeList"
                   :key="item"
                   :label="item"
                   :value="item">
        </el-option>
      </el-select>
      <el-button type="primary"
                 size="small"
                 plain
                 @click="insertTag"></el-button>
      <el-button type="success"
                 size="small"
                 plain
                 @click="loadTags"></el-button>
      <el-button type="danger"
                 size="small"
                 plain
                 @click="deleteTags"></el-button>
      <el-button type="info"
                 size="small"
                 plain
                 :loading="downloadLoading"
                 @click="downloadTable"></el-button>
      <el-button type="info"
                 size="small"
                 plain
                 @click="uploadTable"></el-button>
      <input ref="excel-upload-input"
             class="excel-upload-input"
             type="file"
             accept=".xlsx, .xls"
             @change="handleClick">
    </el-row>

    <!-- table -->
    <el-table :data="dataTags"
              border
              current-row-key
              empty-text=""
              :max-height="tableMaxHeight"
              @selection-change="handleSelectionChange">

      <el-table-column type="selection"
                       width="30">
      </el-table-column>
      <el-table-column prop="index"
                       label=""
                       width="40">
      </el-table-column>

      <!--  -->
      <el-table-column v-for="(column, index) in dataColumns"
                       :key="index"
                       :prop="column.prop"
                       :label="column.label"
                       :width="column.width?column.width:''"
                       :min-width="column.minWidth?column.minWidth:''">
      </el-table-column>

      <!--  -->
      <el-table-column label=""
                       min-width="160">
        <template slot-scope="scope">
          <el-button type="primary"
                     size="mini"
                     plain
                     icon="el-icon-edit"
                     @click="editTag(scope.row)"></el-button>
          <el-button type="danger"
                     size="mini"
                     plain
                     icon="el-icon-delete"
                     style="margin-left:3px"
                     @click="deleteTag(scope.row.id)"></el-button>
          <el-button type="success"
                     size="mini"
                     plain
                     icon="el-icon-top"
                     style="margin-left:3px"
                     :disabled="scope.row.index===1"
                     @click="tagToggle(scope.row.index-1,'up')"></el-button>
          <el-button type="success"
                     size="mini"
                     plain
                     icon="el-icon-bottom"
                     style="margin-left:3px"
                     :disabled="scope.row.index===dataTags.length"
                     @click="tagToggle(scope.row.index-1,'down')"></el-button>
        </template>
      </el-table-column>

    </el-table>

    <!-- dialog  / -->
    <el-dialog class="tag-dialog"
               :title="dialogTitle"
               :visible.sync="dialogVisible">

      <el-form ref="dialogForm"
               :model="formData"
               :rules="formRule"
               label-position="left">

        <el-row :gutter="0">
          <el-col :span="9">
            <el-form-item label-width="65px"
                          label=""
                          prop="name">
              <el-input v-model="formData.name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="9">
            <el-form-item label-width="65px"
                          label=""
                          prop="describe">
              <el-input v-model="formData.describe"></el-input>
            </el-form-item>
          </el-col>
          <el-button style="margin:0 0 20px 10px"
                     @click="setParams"></el-button>
        </el-row>

        <el-row :gutter="0">
          <el-col :span="9">
            <el-form-item label=""
                          prop="dataType">
              <el-select v-model="formData.dataType"
                         placeholder=""
                         style="width:203px">
                <el-option v-for="item in dataTypeList"
                           :key="item"
                           :label="item"
                           :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label=""
                          prop="direction">
              <el-select v-model="formData.direction"
                         placeholder="">
                <el-option v-for="item in directionList"
                           :key="item"
                           :label="item"
                           :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="0">
          <el-col :span="9">
            <el-form-item label-width="155px"
                          label=""
                          prop="acquisitionCycle">
              <el-input v-model="formData.acquisitionCycle"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label-width="100px"
                          label="IO"
                          prop="IOTag">
              <el-input v-model="formData.IOTag"
                        style="width:fit-content"
                        disabled></el-input>
            </el-form-item>
          </el-col>
          <el-button style="margin:0 0 20px 10px"
                     @click="tagSelect"></el-button>
        </el-row>

        <el-row :gutter="0">
          <el-col :span="9">
            <el-form-item label-width="70px"
                          label="ID"
                          prop="slaveStationID">
              <el-input v-model="formData.slaveStationID"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label-width="100px"
                          label=""
                          prop="registerType">
              <el-select v-model="formData.registerType"
                         placeholder=""
                         style="width:250px">
                <el-option v-for="item in registerTypeList"
                           :key="item.value"
                           :label="item.label"
                           :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="0">
          <el-col :span="9">
            <el-form-item label-width="100px"
                          label=""
                          prop="registerAddr">
              <el-input v-model="formData.registerAddr"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label-width="90px"
                          label=""
                          prop="dataFormat">
              <el-select v-model="formData.dataFormat"
                         placeholder=""
                         style="width:250px">
                <el-option v-for="item in dataFormatList"
                           :key="item.value"
                           :label="item.label"
                           :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

      </el-form>

      <div slot="footer"
           class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button @click="tagSubmit"
                   type="primary"
                   :loading="submitLoading"> </el-button>
      </div>
    </el-dialog>

    <!-- dialog   -->
    <el-dialog title=" "
               :visible.sync="dialogVisibleParam">

      <el-checkbox v-model="formData.ratioCalculation"
                   style="margin-right:20px"></el-checkbox>
      <div class="params-dialog-row-div">
        &nbsp;a
        <el-input v-model="formData.magnification"
                  style="width:100px"
                  :disabled="!formData.ratioCalculation"></el-input>
        &nbsp;b
        <el-input v-model="formData.base"
                  style="width:100px;margin-right:20px"
                  :disabled="!formData.ratioCalculation"></el-input>
        y = ax + b
      </div>

      <div slot="footer"
           class="dialog-footer">
        <el-button @click="dialogVisibleParam = false;
                           formData.ratioCalculation = otherParamsOrg.ratioCalculation;
                           formData.magnification = otherParamsOrg.magnification;
                           formData.base = otherParamsOrg.base;"> </el-button>
        <el-button type="primary"
                   @click="dialogVisibleParam = false"> </el-button>
      </div>
    </el-dialog>

    <!-- dialog -  -->
    <tag-select ref="tagSelect"
                :id="id"
                :tag-describe="tagDescribe"
                :tree-data="treeData"
                :form-data="formData"
                :pass-list="passList"
                :equipment-list="equipmentList"
                @tag-click="tagClick"></tag-select>

    <!-- dialog -  -->
    <tag-load ref="tagLoad"
              :tree-data="treeData"
              :data-tags="dataTags"
              :pass-list="passList"
              :equipment-list="equipmentList"></tag-load>

  </div>
</template>

<script>
import { parseTime } from "@/utils"; // functions
import { arraySort } from "@/utils/dataHanding";// function - key
import XLSX from "xlsx"; // plugin - excel
import { passTagColumn, passTagHeader, tagTranslation } from "@/mock/tableColumn";
import TagSelect from "@/components/dialog/tagSelect"; // 
import TagLoad from "@/components/dialog/tagLoad"; // 

export default {
  components: { TagSelect, TagLoad },
  props: {
    //  - tagSelect
    treeData: {
      type: Array,
      default: () => []
    },
    //  - tagSelect
    passList: {
      type: Array,
      default: () => []
    },
    //  - tagSelect
    equipmentList: {
      type: Array,
      default: () => []
    },
    // id
    id: {
      type: String
    },
    //  - 
    dataTagsOrg: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      /* table */
      dataTags: [], //  - 
      dataColumns: passTagColumn, // 
      dataTypeList: ["", "", "", "", "", ""], // select - 
      dataTypeSelect: "", //  - 
      multipleSelection: [], //  - 
      /* dialog */
      dialogVisible: false, // 
      dialogVisibleParam: false, //  - 
      dialogType: "", // insert/edit
      dialogTitle: "", // dialog
      formData: { // 
        name: "",
        describe: "",
        ratioCalculation: false,
        magnification: "1.0000",
        base: "0.0000",
        dataType: "",
        direction: "",
        acquisitionCycle: "1000",
        IOTag: "at._kernal_version",
        IOTagParentId: "1",
        IOTagSelectId: "0",
        slaveStationID: "1",
        registerType: 2,
        registerAddr: "0",
        dataFormat: 0
      },
      formDataOrg: {}, //  - 
      formRule: { // 
        name: [
          { required: true, message: "", trigger: "blur" }
        ],
        describe: [
          { required: true, message: "", trigger: "blur" }
        ],
        acquisitionCycle: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      directionList: ["", "", ""], // select - 
      registerTypeList: [ // select - 
        {
          label: "1",
          value: 0
        },
        {
          label: "2",
          value: 1
        },
        {
          label: "3",
          value: 2
        },
        {
          label: "4",
          value: 3
        }
      ],
      dataFormatList: [ // select - 
        {
          label: "2  21",
          value: 0
        },
        {
          label: "2  12",
          value: 1
        },
        {
          label: "2 1",
          value: 2
        },
        {
          label: "2 2",
          value: 3
        },
        {
          label: "4 4321",
          value: 4
        },
        {
          label: "8 87654321",
          value: 5
        }
      ],
      /* loading */
      submitLoading: false, // loading - 
      downloadLoading: false, // loading - 
      /* props */
      tagDescribe: "",
      /*  */
      tableMaxHeight: 0 // 
    };
  },
  created () {
    const screenHeight = document.documentElement.clientHeight;
    this.tableMaxHeight = screenHeight - 73 - 20 * 2 - (40 + 15) - (40 + 20) - 40;
    this.getData();
  },
  methods: {
    // 
    getData () {
      this.refreshData();
    },
    // 
    refreshData () {
      /*  */
      if (this.dataTypeSelect !== "") {
        this.dataTags = this.dataTagsOrg.filter(tag =>
          tag.dataType === this.dataTypeSelect
        );
      } else {
        this.dataTags = this.dataTagsOrg;
      }
      this.dataTags.sort(arraySort("name", "asc")); // 
      this.dataTags.map((tag, i) => {
        this.$set(tag, "index", i + 1); // 1
      });
      // console.log(this.dataTags);
    },
    // 
    insertTag () {
      this.dialogVisible = true;
      this.dialogType = "insert";
      this.$nextTick(() => {
        this.$refs.dialogForm.resetFields();
        this.dialogTitle = `-${this.dialogType === "insert" ? "" : ""}`;
      });
    },
    //  -  - 
    loadTags () {
      this.$refs.tagLoad.tagLoad();
    },
    // 
    editTag (row) {
      this.dialogVisible = true;
      this.dialogType = "edit";
      this.$nextTick(() => {
        this.$refs.dialogForm.resetFields();
        this.dialogTitle = `-${this.dialogType === "insert" ? "" : ""}`;
        this.formDataOrg = row;
        this.formData = JSON.parse(JSON.stringify(row)); // 
      });
    },
    // 
    setParams () {
      this.dialogVisibleParam = true;
      this.otherParamsOrg = JSON.parse(JSON.stringify( // 
        {
          ratioCalculation: this.formData.ratioCalculation,
          magnification: this.formData.magnification,
          base: this.formData.base
        }
      ));
    },
    //  -  - 
    tagSelect () {
      this.$refs.tagSelect.tagSelect();
    },
    //  - 
    tagClick (param) {
      console.log(param);
      if (param) {
        this.formData.IOTag =
          `${param.source === "IO" ? "at." : "io."}${param.passName ? param.passName + "." : ""}${param.equpimentName ? param.equpimentName + "." : ""}${param.name}`;
        this.formData.IOTagParentId = param.parentId;
        this.formData.IOTagSelectId = param.id;
        this.tagDescribe =
          `${param.passDescribe ? param.passDescribe + " " : ""}${param.equipmentDescribe ? param.equipmentDescribe + " " : ""}${param.describe}`;
      }
    },
    // 
    tagSubmit () {
      const formData = JSON.parse(JSON.stringify(this.formData));
      this.$refs.dialogForm.validate(valid => {
        if (valid) {
          this.submitLoading = true;
          switch (this.dialogType) {
            case "insert":
              if (
                this.dataTags.some(
                  tag => tag.name === this.formData.name
                )
              ) {
                // 
                this.$message.error("");
                this.submitLoading = false;
              } else {
                // id
                formData.id = Math.random()
                  .toString(36)
                  .substr(-10);
                this.dataTags.push(JSON.parse(JSON.stringify(formData)));
                this.refreshData();
                this.submitLoading = false;
                this.dialogVisible = false;
              }
              break;
            case "edit":
              if (
                this.dataTags.some(
                  tag => tag.name === this.formData.name
                ) &&
                this.formData.name !== this.formDataOrg.name
              ) {
                // 
                this.$message.error("");
                this.submitLoading = false;
              } else {
                this.$set(
                  this.dataTags,
                  this.formData.index - 1,
                  JSON.parse(JSON.stringify(formData))
                );
                this.submitLoading = false;
                this.dialogVisible = false;
              }
              break;
          }
        }
      });
    },
    // 
    deleteTag (id) {
      this.$confirm(", ?", "", {
        type: "warning"
      }).then(() => {
        this.dataTags.forEach((tag, i) => {
          tag.id === id && this.dataTags.splice(i, 1);
        });
        this.refreshData();
      }).catch(() => { });
    },
    // 
    handleSelectionChange (val) {
      this.multipleSelection = val;
    },
    // 
    deleteTags () {
      if (this.multipleSelection.length === 0) {
        this.$message({
          message: "",
          type: "warning",
          duration: 1000
        });
        return false;
      }
      this.$confirm(", ?", "", {
        type: "warning"
      }).then(() => {
        for (let i = 0; i < this.dataTags.length; i++) {
          this.multipleSelection.forEach(select => {
            this.dataTags[i].id === select.id && this.dataTags.splice(i, 1);
          });
        }
        this.refreshData();
      }).catch(() => { });
    },
    // 
    tagToggle (index, handle) {
      if (handle === "up") {
        // 
        if (index !== 0) {
          const toExchange = this.dataTags[index - 1];
          this.$set(
            this.dataTags,
            index - 1,
            this.dataTags[index]
          );
          this.$set(this.dataTags, index, toExchange);
          this.refreshData();
        }
      } else {
        // 
        if (index !== this.dataTags.length - 1) {
          const toExchange = this.dataTags[index + 1];
          this.$set(
            this.dataTags,
            index + 1,
            this.dataTags[index]
          );
          this.$set(this.dataTags, index, toExchange);
          this.refreshData();
        }
      }
    },
    // 
    downloadTable () {
      this.downloadLoading = true;
      import("@/vendor/Export2Excel").then(excel => {
        const tHeader = passTagHeader.tHeader;
        const filterVal = passTagHeader.filterVal;
        const list = this.dataTags;
        const data = this.formatJson(filterVal, list);
        excel.export_json_to_excel({
          header: tHeader,
          data,
          filename: "-",
          autoWidth: true,
          bookType: "xlsx"
        });
        this.downloadLoading = false;
      });
    },
    // 
    formatJson (filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => {
        if (j === "timestamp") {
          return parseTime(v[j]);
        } else {
          return v[j];
        }
      }));
    },
    // 
    uploadTable () {
      this.$refs["excel-upload-input"].click();
    },
    //  - 
    handleClick (e) {
      const files = e.target.files;
      const rawFile = files[0];
      if (!rawFile) return;
      this.upload(rawFile);
    },
    //  - 
    upload (rawFile) {
      this.$refs["excel-upload-input"].value = null; // fix can't select the same excel
      const before = this.beforeUpload(rawFile);
      if (before) this.readerData(rawFile);
    },
    // 
    beforeUpload (file) {
      const isLt1M = file.size / 1024 / 1024 < 1;
      if (isLt1M) {
        return true;
      }
      this.$message({
        message: "1MB",
        type: "warning"
      });
      return false;
    },
    // 
    readerData (rawFile) {
      this.loading = true;
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = e => {
          const data = e.target.result;
          const workbook = XLSX.read(data, { type: "array" });
          const firstSheetName = workbook.SheetNames[0];
          const worksheet = workbook.Sheets[firstSheetName];
          const header = this.getHeaderRow(worksheet);
          const results = XLSX.utils.sheet_to_json(worksheet);
          // console.log(header);
          // console.log(results);
          this.generateData({ header, results });
          this.loading = false;
          resolve();
        };
        reader.readAsArrayBuffer(rawFile);
      });
    },
    // 
    getHeaderRow (sheet) {
      const headers = [];
      const range = XLSX.utils.decode_range(sheet["!ref"]);
      let C;
      const R = range.s.r;
      /* start in the first row */
      for (C = range.s.c; C <= range.e.c; ++C) { /* walk every column in the range */
        const cell = sheet[XLSX.utils.encode_cell({ c: C, r: R })];
        /* find the cell in the first row */
        let hdr = "UNKNOWN " + C; // <-- replace with your desired default
        if (cell && cell.t) hdr = XLSX.utils.format_cell(cell);
        headers.push(hdr);
      }
      return headers;
    },
    // 
    generateData ({ header, results }) {
      /* 1.resultsdataTags */
      results.forEach(row => {
        Object.keys(row).forEach(key => {
          // console.log(key);
          Object.keys(tagTranslation).forEach(_key => {
            if (key === tagTranslation[_key]) {
              this.$set(row, _key, row[key]);
              this.$delete(row, key);
            }
          });
        });
      });
      // console.log(results);
      /* 2.dataTagsOrg */
      this.dataTagsOrg.splice(0, this.dataTagsOrg.length);
      /* 3.resultsdataTagsOrg */
      results.forEach(row => {
        this.dataTagsOrg.push(row);
      });
      this.refreshData();
    }
  },
  watch: {
    id (val) {
      this.getData();
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
</style>



