<template>
  <el-dialog class="tag-load-dialog"
             v-if="dialogVisible"
             title="IO"
             :visible.sync="dialogVisible">

    <el-row class="params-dialog-row">
      <el-checkbox v-model="filterForm.passDescribe"></el-checkbox>
      <el-checkbox v-model="filterForm.equipmentDescribe"></el-checkbox>
      <el-checkbox v-model="filterForm.equipmentStatusTag"></el-checkbox>
    </el-row>

    <el-row class="params-dialog-row">
      <el-checkbox v-model="filterForm.floatingPoint"></el-checkbox>
      <el-checkbox v-model="filterForm.parseInt"></el-checkbox>
      <el-checkbox v-model="filterForm.boolean"></el-checkbox>
      <el-checkbox v-model="filterForm.string"></el-checkbox>
      <el-checkbox v-model="filterForm.binary"></el-checkbox>
    </el-row>

    <el-table :data="tableData"
              current-row-key
              empty-text=""
              @selection-change="handleSelectionChange">
      <el-table-column type="selection"
                       min-width="30">
      </el-table-column>
      <el-table-column type="index"
                       label=""
                       min-width="40">
      </el-table-column>
      <el-table-column label=""
                       min-width="110">
        <template slot-scope="scope">
          {{scope.row.passName+'.'+scope.row.equipmentName}}
        </template>
      </el-table-column>
      <el-table-column label=""
                       min-width="140">
        <template slot-scope="scope">
          {{scope.row.passDescribe+'.'+scope.row.equipmentDescribe}}
        </template>
      </el-table-column>
    </el-table>

    <div slot="footer"
         class="dialog-footer">
      <el-button @click="dialogVisible = false"> </el-button>
      <el-button type="primary"
                 @click="submit"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
import { arraySort } from "@/utils/dataHanding";// key

export default {
  props: {
    // id
    id: {
      type: String
    },
    // 
    formData: {
      type: Object,
      default: () => { }
    },
    //  - 
    treeData: {
      type: Array,
      default: () => []
    },
    // 
    passList: {
      type: Array,
      default: () => []
    },
    // 
    equipmentList: {
      type: Array,
      default: () => []
    },
    // 
    dataTags: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      dialogVisible: false, // 
      tableData: [], // 
      multipleSelection: [], //  - 
      filterForm: {} // 
    };
  },
  methods: {
    // 
    tagLoad () {
      this.dialogVisible = true;
      this.refreshData();
      this.filterForm = { // 
        passDescribe: true,
        equipmentDescribe: true,
        equipmentStatusTag: true,
        floatingPoint: true,
        parseInt: true,
        boolean: true,
        string: true,
        binary: true
      };
    },
    // 
    refreshData () {
      /*  */
      this.copyTreeData = JSON.parse(JSON.stringify(this.treeData));
      this.copyEquipmentList = JSON.parse(JSON.stringify(this.equipmentList));
      /*  */
      this.copyTreeData.forEach(group => {
        group.id === "2" && (group.children = []);
        group.children.forEach(pass => {
          this.passList.forEach(_pass => {
            pass.id === _pass.id && pass.children.forEach(equipment => {
              this.copyEquipmentList.forEach(_equipment => {
                if (equipment.id === _equipment.id) {
                  // console.log(_equipment);
                  _equipment.passName = _pass.passName;
                  _equipment.passDescribe = _pass.passDescribe;
                }
              });
            });
          });
        });
      });
      // console.log(this.copyEquipmentList);
      this.tableData = this.copyEquipmentList;
    },
    // 
    handleSelectionChange (val) {
      this.multipleSelection = val;
    },
    // 
    submit () {
      // console.log(this.filterForm);
      console.log(this.multipleSelection);

      /* 1. */
      const tagListTemporary = [];
      this.multipleSelection.forEach(row => {
        this.filterForm.equipmentStatusTag && tagListTemporary.push({ //  - _io_status
          id: Math.random().toString(36).substr(-10),
          name: `${row.passName}.${row.equipmentName}._io_status`,
          describe: `${this.filterForm.passDescribe ? row.passDescribe + " " : ""}${this.filterForm.equipmentDescribe ? row.equipmentDescribe + " " : ""}`,
          ratioCalculation: false,
          magnification: "2.0000",
          base: "0.0000",
          dataType: "",
          direction: "",
          acquisitionCycle: "1000",
          IOTag: `at.${row.passName}.${row.equipmentName}._io_status`,
          IOTagParentId: row.id,
          IOTagSelectId: "3",
          slaveStationID: "1",
          registerType: 0,
          registerAddr: "0",
          dataFormat: 0
        });
        row.dataTags.forEach(tag => {
          // console.log(tag);
          if (
            (this.filterForm.floatingPoint && tag.dataType === "") ||
            (this.filterForm.parseInt && tag.dataType === "") ||
            (this.filterForm.boolean && tag.dataType === "") ||
            (this.filterForm.string && tag.dataType === "") ||
            (this.filterForm.binary && tag.dataType === "")
          ) {
            tagListTemporary.push({ //  - 
              id: Math.random().toString(36).substr(-10),
              name: `${row.passName}.${row.equipmentName}.${tag.name}`,
              describe: `${this.filterForm.passDescribe ? row.passDescribe + " " : ""}${this.filterForm.equipmentDescribe ? row.equipmentDescribe + " " : ""}${tag.describe}`,
              ratioCalculation: false,
              magnification: "2.0000",
              base: "0.0000",
              dataType: tag.dataType,
              direction: tag.direction,
              acquisitionCycle: tag.acquisitionCycle,
              IOTag: `io.${row.passName}.${row.equipmentName}.${tag.name}`,
              IOTagParentId: row.id,
              IOTagSelectId: tag.id,
              slaveStationID: "1",
              registerType: 0,
              registerAddr: "0",
              dataFormat: 0
            });
          }
        });
      });
      // console.log(this.dataTags);

      /* 2.vsname */
      // console.log(tagListTemporary);
      const addTags = tagListTemporary.filter(tag => {
        return !this.dataTags.some(_tag => _tag.name === tag.name);
      });
      // console.log(addTags);

      /* 3. */
      addTags.forEach(tag => {
        this.dataTags.push(tag);
      });
      this.dataTags.sort(arraySort("name", "asc")); // 
      this.dataTags.map((tag, i) => {
        this.$set(tag, "index", i + 1); // 1
      });

      this.dialogVisible = false;
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
</style>



